$notification-max-height: 400px; // css can't transition from height 0 to height unknown, so we need to keep our notification messages short.

// default (floaty) notifications container (not inside a modal)
.notification-container {
  z-index: $zindex-toast;
  position: fixed;
  top: $navbar-height + 20px;
  right: 20px;
  width: 100%;
  transition: all 0.4s ease-in-out;
  display: none;
  background: rgba(mix($popup-bg-color, $body-bg, 33%), 0.5);
  @include desktop-only {
    max-width: $modal-full-width-sm;
    display: block;
  }
  // notification alerts
  .notification-feed {
    overflow: hidden;
    .notification-alert {
      float: right;
      width: 100%;
      clear: both;
      background: $popup-bg-color;
      color: $popup-text-color;
      padding: $modal-inner-padding;
      &:not(.out) ~ :not(.out) {
        margin-top: 1px;
      }
      .heading {
        font-size: $font-size-base;
        color: $popup-text-color;
        font-weight: $font-weight-bold;
        margin: -0.25em 0 1rem 0;
      }
      .message {
        line-height: $line-height-small;
        font-size: $font-size-small;
        font-weight: $font-weight-light;
        margin-bottom: 0;
        + .message {
          margin-top: 1rem;
        }
        a {
          color: $popup-text-color;
          text-decoration: underline;
        }
      }
      .footnote {
        font-family: $font-family-sans-serif;
        font-size: $font-size-xsmall;
        opacity: 0.6;
        margin: 0.5em 0 -0.66em 0;
      }
      .close {
        color: white;
        opacity: 0.75;
        position: absolute;
        top: 6px;
        right: 6px;
        padding: 15px;
        @include fa-icon;
        &:before { content: $fa-var-trash-o; }
        &:hover {
          opacity: 1;
        }
      }
    }
  }
  // show more link at the bottom
  .show-more {
    font-size: $font-size-small;
    display: block;
    cursor: pointer;
    padding: $padding-base-vertical $modal-inner-padding;
    background-color: $popup-bg-color;
    margin-top: 1px;
    color: rgba(white, 0.8);
    &:hover {
      color: white;
    }
    &#{&} {
      transition: color 0s;
    }
    &.fade:not(.in) {
      margin: 0;
    }
  }
  .notification-alert, .show-more.in {
    border: 1px solid rgba($gray-base, 0.1);
  }
  // show/hide transitions
  .fade {
    transition: all 0.4s ease-in-out;
    opacity: 0;
    &:not(.in) {
      pointer-events: none;
      max-height: 0;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: -200px;
      transform: translate(0, -100%);
    }
    &.in {
      opacity: 1;
      transform: translate(0, 0);
      max-height: $notification-max-height;
      ~ :not(.in) {
        margin-bottom: -100px;
        transform: translate(0, 0);
      }
    }
  }
}

// hide floaty notifications when modal opens
.modal-open {
  > .notification-container {
    transform: translate(150%, 0);
    opacity: 0;
  }
}
// notifications inside the modal
.full-modal.notification-modal {
  // modal alterations
  .modal-header {
    margin-bottom: 0;
  }
  .delete-all {
    padding: 10px $modal-inner-padding 6px $modal-inner-padding;
    font-size: $font-size-small;
    &, a {
      color: rgba(white, 0.8);
    }
    a {
      cursor: pointer;
      &:hover {
        color: white;
      }
    }
  }
  .modal-body {
    top: $navbar-height + 38px;
    &.scrollbars {
      > .modal-body {
        top: 0;
        &:after {
          content: none; // remove the bottom margin
        }
      }
    }
  }
  // notifications alterations
  .notification-container {
    display: block;
    overflow-y: auto;
    position: static;
    background: none;
    @include desktop-only {
      max-width: $modal-full-width-sm;
    }
    .show-more {
      display: none;
    }
    // make all notifications visible with css
    .notification-alert {
      padding-left: 0;
      padding-right: 0;
      margin-top: 0;
      margin-bottom: 0;
      &:not(:first-child) {
        border-top: 1px solid rgba(white, 0.25);
      }
      &.out {
        border-top: none;
      }
      &.out:first-child + .fade {
        border-top: none;
      }
      .close {
        top: 0;
        right: -16px;
      }
      &.fade {
        &:not(.out) {
          opacity: 1;
          pointer-events: all;
          max-height: $notification-max-height;
          padding-top: $modal-inner-padding;
          padding-bottom: $modal-inner-padding;
        }
        &:not(.in),
        &.in ~ :not(.in) {
          margin-bottom: 0;
        }
      }
    }
  }
  .notification-alert, .show-more {
    border: none;
  }
}
