.site-footer {
  // base styles
  font-family: $font-family-sans-serif;
  font-size: $font-size-xsmall;
  background: $footer-bg;
  color: $text-color;
  text-align: center;
  padding: 15px 0;
  overflow: hidden;
  .row {
    margin: 0;
  }
  // copyright statement and inline links
  p, ul, ol {
    margin: 0 0 -0.125em 0;
  }
  .footer-copyright,
  .footer-links {
    float: none;
    text-align: center;
  }
  .footer-links {
    li:not(:last-child):after {
      color: $text-muted;
      content: '|';
      margin: 0 0 0 14px;
    }
  }
  @media (min-width: $screen-lg) {
    .footer-copyright {
      float: left;
    }
    .footer-links {
      float: right;
    }
  }
}

// make the footer stick to the bottom of the window/page (using position absolute)
.sticky-footer-layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  .page-wrapper {
    flex: 1 0 auto;
  }
  .site-footer {
    flex: 0 0 auto;
  }
}
