.outdated-browser {
  z-index: $zindex-outdated-browser;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 16px 14px 16px;
  background: $brand-warning;
  &, * {
    color: white;
  }
  h6 {
    font-size: $font-size-h2;
    margin: 0;
  }
  p {
    margin: 0;
  }
  a {
    font-weight: $font-weight-bold;
    text-decoration: underline;
    &:hover,
    &:focus {
      color: white;
    }
  }
  #btnCloseUpdateBrowser {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 16px;
    font-size: 32px;
    text-decoration: none;
  }
}
