// substitude Glyphicons with Font Awesome
.carousel-control {

  .icon-prev,
  .glyphicon-chevron-left,
  .icon-next,
  .glyphicon-chevron-right {
    margin-top: -1.5em;
    &:before {
      line-height: 1;
      font-size: 3em;
      font-family: FontAwesome;
    }
  }

  .icon-prev,
  .glyphicon-chevron-left {
    &:before {
      content: $fa-var-angle-left;
    }
  }

  .icon-next,
  .glyphicon-chevron-right {
    &:before {
      content: $fa-var-angle-right;
    }
  }
}
