@keyframes siteSearchFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes siteSearchIconFadeIn {
  from { left: 400px; }
  to { left: 22px; }
}

@keyframes siteSearchInputFadeIn {
  from { padding-left: 400px + $navbar-height; }
  to { padding-left: $navbar-height; }
}

// global search component in header
.site-search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 125%;
  z-index: $zindex-site-search;
  display: none;
  background-color: rgba($modal-backdrop-bg, $modal-backdrop-opacity);

  > .search-bar {
    background: $brand-secondary;
    height: $navbar-height;
    color: $menu-link-color;
    &:before {
      position: absolute;
      font-family: FontAwesome;
      content: $fa-var-search;
      font-size: 18px;
      top: 16px;
      left: 22px;
      pointer-events: none;
    }
    .search-input {
      width: 100%;
      height: 98%;
      padding: 0 ($navbar-height - 10) 0 $navbar-height;
      font-size: 22px;
      background: transparent;
      border: none;
      outline: none;
      font-weight: $font-weight-light;
      @include placeholder-selector {
        font-style: italic;
        color: rgba($menu-base-color, 0.5);
      }
      &::-ms-clear {
        display: none;
      }
    }
    .close {
      color: $popup-link-color;
      position: absolute;
      top: 6px;
      right: 10px;
    }
  }

  > .search-dropdown {
    background: $popup-bg-color;
    overflow-y: auto;
    max-height: calc(80% - #{$navbar-height}); // 80% of parent (125%) = 100%
  }

  .search-results {
    padding: 0;
    margin: 0;
    li {
      position: relative;
      &:not(:first-child):before {
        content: '';
        border-bottom: 1px solid $popup-line-color;
        position: absolute;
        display: block;
        right: $navbar-height;
        left: $navbar-height;
        top: 0;
        @include mobile-only {
          right: 20px;
          left: 20px;
        }
      }

      // each search result
      a {
        text-decoration: none;
        display: block;
        padding: 18px 20px 18px $navbar-height;
        @include mobile-only {
          padding-left: 20px;
        }
        .breadcrumb {
          color: $popup-text-color;
          margin-bottom: 2px;
        }
        .title {
          margin: 0;
          color: $popup-text-color;
          font-size: 18px;
        }
      }
      &:hover,
      &.active {
        &:before,
        + li:before {
          display: none;
        }
        a {
          background: $brand-info;
        }
      }

      // the text message at the bottom of the list
      &.search-status {
        font-size: $font-size-small;
        font-weight: $font-weight-normal;
        padding: 8px 20px 8px $navbar-height;
        @include mobile-only {
          padding-left: 20px;
        }
        &, a {
          color: $popup-link-color;
        }
        &:hover:before {
          display: inherit;
        }
      }
    }
  }
}

.site-search-open {
  .site-search {
    animation: siteSearchFadeIn $default-transition;
    display: block;
    .search-bar:before {
      animation: siteSearchIconFadeIn $default-transition;
    }
    .search-input {
      animation: siteSearchInputFadeIn $default-transition;
    }
  }
  .page-wrapper {
    // a way to get the scrolling working properly on iOS safari
    height: 100%;
  }
}
