.toast-alert {
  z-index: $zindex-toast;
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba($gray-base, 0.9);
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-right: -50%;
  padding: 12px 18px;
  transform: translate(-50%, 0);
  text-align: center;
  width: 101%;
  min-width: 280px;
  // box-shadow: 1px 1px 0 rgba(86, 86, 86, 0.25);
  @media (min-width: 560px) {
    width: auto;
    max-width: 560px;
  }
  &.fade {
    opacity: 0;
    transform: translate(-50%, 100%);
    transition: all $default-transition;
    &.in {
      opacity: 1;
      transform: translate(-50%, 0);
    }
  }
  .fa {
    margin-right: 9px;
  }

  // color variations
  &.toast-primary {
    color: rgba($btn-primary-color, 0.9);
    background-color: rgba($brand-primary, 0.9);
  }
  &.toast-secondary {
    color: rgba($btn-secondary-color, 0.9);
    background-color: rgba($brand-secondary, 0.9);
  }
  &.toast-info {
    color: rgba($btn-info-color, 0.9);
    background-color: rgba($brand-info, 0.9);
  }
  &.toast-success {
    color: rgba($btn-success-color, 0.9);
    background-color: rgba($brand-success, 0.9);
  }
  &.toast-warning {
    color: rgba($btn-warning-color, 0.9);
    background-color: rgba($brand-warning, 0.9);
  }
  &.toast-danger {
    color: rgba($btn-danger-color, 0.9);
    background-color: rgba($brand-danger, 0.9);
  }
}

