.input-group-addon {
  background: $input-border;
  font-size: $input-font-size-base;
  color: $input-group-addon-color;
  // line-height: 1; // fix for addons getting too tall
  // border: none;
  border: 1px solid transparent;
  .input-group & {
    padding-top: 0;
    padding-bottom: 0;
  }
  .form-group-sm &,
  .input-group-sm & {
    font-size: $font-size-small;
  }
  .form-group-lg &,
  .input-group-lg & {
    font-size: $font-size-large;
  }
}

