.btn,
.form-control {
  // Fix dropdowns LEFT border-radius
  .input-group .dropup:first-child &,
  .input-group .dropdown:first-child & {
    @include border-left-radius($border-radius-base);
  }
  .input-group .dropup:not(:first-child) &,
  .input-group .dropdown:not(:first-child) & {
    @include border-left-radius(0);
  }
  // Fix dropdowns RIGHT border-radius
  .input-group .dropup:last-child &,
  .input-group .dropdown:last-child & {
    @include border-right-radius($border-radius-base);
  }
  .input-group .dropup:not(:last-child) &,
  .input-group .dropdown:not(:last-child) & {
    @include border-right-radius(0);
  }
}
// Bootstrap does this anoying thing where they @extend .input-sm and .input-lg so now we need to undo some of that
// .input-group-lg > .form-control,
// .input-group-lg > .input-group-addon,
.input-group-lg > .input-group-btn > .btn {
  line-height: 1;
}

// .input-group-sm > .form-control,
// .input-group-sm > .input-group-addon,
.input-group-sm > .input-group-btn > .btn {
  line-height: 1;
}
