// gutter classes are used to add vertical white-space to elements - these need to be defined early so that other classes can @extend them
%top-gutter { @include responsive-vertical-gutter(margin-top); }
%bottom-gutter { @include responsive-vertical-gutter(margin-bottom); }

// Body reset
html,
body {
  overflow-x: hidden; // for offcanvas menu stuff - prevents a dom scroll on narrow devices
}

body {
  overflow: hidden; // prevents a second scrollbar appearing for position absolute elements that extend beyond the bottom of the page
  min-width: 320px;
  font-weight: $font-weight-normal;
  letter-spacing: normal;
  // There's a weird thing that Chrome does. Resizing the browser to < 768px the font changes from Google's font to a locally installed version which looks a bit different, and this seems to prevent that:
  text-rendering: optimizeLegibility;
  // required by IE 11 for sticky-footer-layout to work:
  display: flex;
  flex-direction: column;
}

a {
  cursor: pointer; // to allow ommitting of [href]
  &:hover {
    text-decoration: underline;
  }
  &:active {
    color: $link-active-color;
    text-decoration: underline;
  }
}

// bootstrap has .img-responsive class, but it is tedious to have to add it to every single img tag... this should be the default behavior of images.
// Use .img for SVG that should behave like regular img
img,
.img {
  max-width: 100%;
  height: auto;
  // center by default (most of the time we want images centered):
  display: block;
  margin-left: auto;
  margin-right: auto;
  // prevent image stretching in IE 11 caused by flex:
  flex: none;
  // Image alignment utilities
  &.align--left {
    margin-left: 0;
  }
  &.align--right {
    margin-right: 0;
  }
}

.img-xs { width: 200px; }
.img-sm { width: 260px; }
.img-md { width: 410px; }
.img-lg { width: 710px; }
.img-xl { width: 1400px; }

/* stylelint-disable */
// We need to target IE and tell it how to deal with SVG to prevent the image height from collapsing
// The below is a valid media query that will target IE10 and later (not Edge)
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .img {
    &.img-xs {
      min-height: 200px;
    }
    &.img-sm {
      min-height: 260px;
    }
    &.img-md {
      min-height: 410px;
    }
    &.img-lg {
      min-height: 710px;
    }
    &.img-xl {
      min-height: 1400px;
    }
  }
}
/* stylelint-enable */

// default horizontal-rule (divider)
hr {
  clear: both;
  margin: $vertical-gutter 0;
}

// width helpers (as per Bootstrap v4)
// .w-25 { width: 25% !important; } /* stylelint-disable-line */
// .w-50 { width: 50% !important; } /* stylelint-disable-line */
// .w-75 { width: 75% !important; } /* stylelint-disable-line */
.w-100 { width: 100% !important; } /* stylelint-disable-line */
.w-auto { width: auto !important; } /* stylelint-disable-line */

// simple css helpers:
.no-padding { padding: 0 !important; } /* stylelint-disable-line */
.no-underline { text-decoration: none !important; } /* stylelint-disable-line */
.no-transition { transition: none !important; } /* stylelint-disable-line */
.no-underline { text-decoration: none !important; } /* stylelint-disable-line */
.no-margin:not(.scroll-content) { margin: 0 !important; } /* stylelint-disable-line */
.no-margin-top { margin-top: 0 !important; } /* stylelint-disable-line */
.no-margin-bottom { margin-bottom: 0 !important; } /* stylelint-disable-line */

// responsive css helpers:
.top-gutter:not(.scroll-content) { @include responsive-vertical-gutter(margin-top, 1, !important); }
.bottom-gutter:not(.scroll-content) { @include responsive-vertical-gutter(margin-bottom, 1, !important); }

// theme visibility helpers:
.hidden-dark, .hidden-light { display: inherit; }
.hidden-#{$theme} { display: none; }

/* stylelint-disable */
// brand theme visibility helpers:
// cannot be used on the same element as light/dark theme selectors
.hidden-dd, .hidden-ntt { display: inherit; }
.hidden-#{$brand-theme} { display: none !important; }
// the inverse of .hidden-#{$brand-theme} utility to only display on certain brands
// use this utility wisely as display: block is set, which may conflict with other display settings
// this utility is best used on an element with no other properties set, as a wrapper for content
.show-dd, .show-ntt { display: none !important; }
.show-#{$brand-theme} { display: block !important; }
/* stylelint-enable */
