.label {
  display: inline-block;
  padding: 0 20px;
  line-height: 30px;
  font-size: $input-font-size-base;
  margin: 6px 5px 6px 0;
  border-radius: 0;
  font-weight: $font-weight-normal;
  background-color: $label-bg;
  border: 1px solid transparent;
  .close {
    background-color: $label-close-bg;
    font-size: 18px;
    line-height: 30px;
    width: 32px;
    height: 32px;
    float: left;
    position: relative;
    left: -20px;
    color: $label-color;
    padding: 1px;
    margin: -1px;
    &:hover {
      opacity: 1;
    }
  }
  &.label-primary {
    @include label-variant-style($brand-primary, white, transparent, $label-close-bg);
  }
  &.label-secondary {
    @include label-variant-style($label-secondary-bg, white, transparent, $label-close-bg);
  }
  &.label-info {
    @include label-variant-style($brand-info, white, transparent, $label-close-bg);
  }
  &.label-success {
    @include label-variant-style($brand-success, white, transparent, $label-close-bg);
  }
  &.label-warning {
    @include label-variant-style($brand-warning, $label-warning-color, transparent, $label-close-bg);
  }
  &.label-danger {
    @include label-variant-style($brand-danger, white, transparent, $label-close-bg);
  }
}
