// we have to repeat this mixin so that it can over-ride other css rules after it (like .input-group-addon)
// don't worry though, duplicate css rules automatically get squashed in post processing (cssnano)
@include text-emphasis-variant('.text-primary', $brand-primary);
@include bg-variant('.bg-primary', $brand-primary);

// New Colour classes:
// $brand-secondary, black and white, $gray-*
@include text-emphasis-variant('.text-secondary', $brand-secondary);
@include bg-variant('.bg-secondary', $brand-secondary);

// action colours (bootstrap uses the $state-* variables for these, which doesn't make much sense, so lets override)
@include text-emphasis-variant('.text-info', $brand-info);
@include bg-variant('.bg-info', $brand-info);

@include text-emphasis-variant('.text-success', $brand-success);
@include bg-variant('.bg-success', $brand-success);

@include text-emphasis-variant('.text-warning', $brand-warning);
@include bg-variant('.bg-warning', $brand-warning);

@include text-emphasis-variant('.text-danger', $brand-danger);
@include bg-variant('.bg-danger', $brand-danger);

// black and white
@include text-emphasis-variant('.text-white', white);
@include bg-variant('.bg-white', white);

@include text-emphasis-variant('.text-black', black);
@include bg-variant('.bg-black', black);

// grays
@include text-emphasis-variant('.text-gray-base', $gray-base);
@include bg-variant('.bg-gray-base', $gray-base);

@include text-emphasis-variant('.text-gray-darkest', $gray-darkest);
@include bg-variant('.bg-gray-darkest', $gray-darkest);

@include text-emphasis-variant('.text-gray-darker', $gray-darker);
@include bg-variant('.bg-gray-darker', $gray-darker);

@include text-emphasis-variant('.text-gray-dark', $gray-dark);
@include bg-variant('.bg-gray-dark', $gray-dark);

@include text-emphasis-variant('.text-gray', $gray);
@include bg-variant('.bg-gray', $gray);

@include text-emphasis-variant('.text-gray-light', $gray-light);
@include bg-variant('.bg-gray-light', $gray-light);

@include text-emphasis-variant('.text-gray-lighter', $gray-lighter);
@include bg-variant('.bg-gray-lighter', $gray-lighter);

@include text-emphasis-variant('.text-gray-lightest', $gray-lightest);
@include bg-variant('.bg-gray-lightest', $gray-lightest);

// Font style utility classes

/* stylelint-disable */
.font-brand { font-family: $font-family-brand !important; }
.font-sans-serif { font-family: $font-family-sans-serif !important; }
.font-monospace { font-family: $font-family-monospace !important; }
.font-serif { font-family: $font-family-serif !important; }
.font-light { font-weight: $font-weight-light !important; }
.font-normal { font-weight: $font-weight-normal !important; }
.font-bold { font-weight: $font-weight-bold !important; }
.font-italic { font-style: italic !important; }
.font-underline { text-decoration: underline !important; }
.font-break-word { word-wrap: break-word !important; }
/* stylelint-enable */


// Headings

// larger headings on larger screens:
@include large-only {
  h1, .h1 { font-size: $font-size-large-h1; }
  h2, .h2 { font-size: $font-size-large-h2; }
  h3, .h3 { font-size: $font-size-large-h3; }
  h4, .h4 { font-size: $font-size-large-h4; }
  h5, .h5 { font-size: $font-size-large-h5; }
  h6, .h6 { font-size: $font-size-large-h6; }
}

// headings' margins:
@include all-headings {
  margin-top: $vertical-gutter;
  margin-bottom: $font-size-base;
  &:first-child {
    margin-top: -0.15em;
  }
  &:last-child {
    margin-bottom: -0.15em;
  }
}

// special headings:
.main-heading {
  font-weight: $font-weight-bold;
  position: relative;
  padding-bottom: 16px;
  display: table; // allows dynamic width (for the underline), while clearing without an extra '.clear-fix' div
  &:after {
    content: '';
    display: block;
    position: absolute;
    border-bottom: 4px solid $brand-primary;
    min-width: 110px;
    width: 70%;
    bottom: 0;
    left: 0.2rem;
  }
}

h1.main-heading {
  padding-bottom: 14px;
}

.sub-heading {
  margin-bottom: 8px;
  font-size: $font-size-large-h1 / 2;
  // .sub-heading now has a value of half the font size of .main-heading
  font-weight: $font-weight-light;
  .info {
    font-weight: $font-weight-normal;
    font-style: italic;
  }
}

.accent-heading {
  margin-bottom: 8px;
  font-family: $font-family-serif;
  font-size: $font-size-base;
  font-weight: $font-weight-normal;
  font-style: italic;
}

.subscript-heading {
  margin-bottom: 8px;
  font-size: $font-size-base - 1;
  font-weight: $font-weight-bold;
}

// Paragraphs
p {
  // margin-top: -0.15em;
  margin-top: -2px; // text-height is more consistent if we use a whole number of pixels
  margin-bottom: 1.5em;
}

// Lists (default style)
ul, ol {
  list-style-type: none;
  margin-bottom: 1.5em;
}

ul:not([class]),
ol:not([class]) {
  padding-left: 1.5em;
  &:last-child {
    margin-bottom: 0;
  }
  li {
    position: relative;
    &:not(:last-child) {
      margin-bottom: 0.75em;
    }
    &:before {
      position: absolute;
      border-radius: 100%;
    }
  }
}

ul:not([class]) {
  li {
    &:before {
      content: '';
      border: 1px solid $text-color;
      width: 6px;
      height: 6px;
      top: 10px;
      left: -20px;
    }
  }
}

ol:not([class]) {
  counter-reset: item;
  li {
    &:before {
      counter-increment: item;
      content: counter(item);
      background: $ol-bullet-bg;
      color: $ol-bullet-color;
      text-align: center;
      transform: scale(0.5); // this seems to be the way to position things with 0.5 pixel accuracy
      font: normal 22px $font-family-base;
      top: -1px;
      left: -29px;
      line-height: 25px;
      width: 27px;
      height: 27px;
    }
  }
}

dt {
  font-family: $font-family-sans-serif;
  font-size: 11px;
  padding-top: 2px;
  margin-right: 3px;
  color: $text-muted;
  font-weight: $font-weight-normal;
  line-height: $line-height-base;
}

dd {
  font-size: $font-size-small;
  color: $text-muted;
  font-weight: $font-weight-normal;
  line-height: $line-height-base;
}

.dl-inline {
  margin: 0 0 -0.5rem 0;
  overflow: hidden;
  dt, dd {
    float: left;
  }
  dt {
    clear: left;
  }
  @media (min-width: $dl-horizontal-breakpoint) {
    dt, dd {
      display: inline;
      float: none;
      clear: none;
    }
    dd {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}
