//
// Responsive: Utility classes [Everything in this file is copied from Bootstrap's grid sizes for our new XL and XXL]
// --------------------------------------------------


// Visibility utilities (added xl and xxl)
// Note: Deprecated .visible-xs, .visible-sm, .visible-md, and .visible-lg as of v3.2.0

@include responsive-invisibility('.visible-xl');
@include responsive-invisibility('.visible-xxl');

.visible-xl-block,
.visible-xl-inline,
.visible-xl-inline-block,
.visible-xxl-block,
.visible-xxl-inline,
.visible-xxl-inline-block {
  display: none !important; /* stylelint-disable-line */
}


// Fix the lg visibility to impliment max-width (added xl and xxl grids)
@media (min-width: $screen-xl) {
  @include responsive-invisibility('.visible-lg');
  @include responsive-visibility('.hidden-lg');
}

.visible-lg-block {
  @media (min-width: $screen-xl) {
    display: none !important; /* stylelint-disable-line */
  }
}

.visible-lg-inline {
  @media (min-width: $screen-xl) {
    display: none !important; /* stylelint-disable-line */
  }
}

.visible-lg-inline-block {
  @media (min-width: $screen-xl) {
    display: none !important; /* stylelint-disable-line */
  }
}


// Add xl visibility
@media (min-width: $screen-xl) and (max-width: $screen-xl-max) {
  @include responsive-visibility('.visible-xl');
}

@media (min-width: $screen-xl) and (max-width: $screen-xl-max) {
  @include responsive-invisibility('.hidden-xl');
}

.visible-xl-block {
  @media (min-width: $screen-xl) and (max-width: $screen-xl-max) {
    display: block !important; /* stylelint-disable-line */
  }
}

.visible-xl-inline {
  @media (min-width: $screen-xl) and (max-width: $screen-xl-max) {
    display: inline !important; /* stylelint-disable-line */
  }
}

.visible-xl-inline-block {
  @media (min-width: $screen-xl) and (max-width: $screen-xl-max) {
    display: inline-block !important; /* stylelint-disable-line */
  }
}


// Add xxl visibility (the biggest possible)
@media (min-width: $screen-xxl) {
  @include responsive-visibility('.visible-xxl');
}

@media (min-width: $screen-xxl) {
  @include responsive-invisibility('.hidden-xxl');
}

.visible-xxl-block {
  @media (min-width: $screen-xxl) {
    display: block !important; /* stylelint-disable-line */
  }
}

.visible-xxl-inline {
  @media (min-width: $screen-xxl) {
    display: inline !important; /* stylelint-disable-line */
  }
}

.visible-xxl-inline-block {
  @media (min-width: $screen-xxl) {
    display: inline-block !important; /* stylelint-disable-line */
  }
}

// New convencience classes around the desktop/mobile main layout breakpoint
.hidden-mobile {
  @include mobile-only {
    display: none !important; /* stylelint-disable-line */
  }
}

.hidden-desktop {
  @include desktop-only {
    display: none !important; /* stylelint-disable-line */
  }
}
