// creates an image for a dropdown caret in a specified solid color
@mixin background-caret($color) {
  $color-code: str-slice(#{$color}, 2);
  $color-hex: '%23#{$color-code}';
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20256%20448%22%20enable-background%3D%22new%200%200%20256%20448%22%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E.arrow%7Bfill%3A#{$color-hex}%3B%7D%3C%2Fstyle%3E%3Cpath%20class%3D%22arrow%22%20d%3D%22M255.9%20168c0-4.2-1.6-7.9-4.8-11.2-3.2-3.2-6.9-4.8-11.2-4.8H16c-4.2%200-7.9%201.6-11.2%204.8S0%20163.8%200%20168c0%204.4%201.6%208.2%204.8%2011.4l112%20112c3.1%203.1%206.8%204.6%2011.2%204.6%204.4%200%208.2-1.5%2011.4-4.6l112-112c3-3.2%204.5-7%204.5-11.4z%22%2F%3E%3C%2Fsvg%3E%0A');
  background-size: 8px 12px;
  background-repeat: no-repeat;
  background-position: right 15px center;
}

@mixin form-control-validation($text-color: #555, $border-color: #ccc, $background-color: #f5f5f5) {
  // Color the label and help text
  .help-block,
  // .control-label,
  .radio,
  .checkbox,
  .radio-inline,
  .checkbox-inline,
  &.radio label,
  &.checkbox label,
  &.radio-inline label,
  &.checkbox-inline label {
    color: $text-color;
  }

  // outline for radios/checkboxes
  .checkbox label:before,
  .radio label:before {
    border-color: $text-color;
  }

  // Set the border and box shadow on specific inputs to match
  .form-control:not(:focus) {
    background-color: $background-color;
    border-color: $border-color;
    &:not(:focus):not(:focus) { // need more specificity esp for css dropdown hover color
      color: $text-color;
    }
    @include placeholder-selector {
      color: $text-color;
    }
  }
  // Set validation states also for addons
  .input-group-addon {
    color: $text-color;
    border-color: $border-color;
    background-color: $background-color;
  }
  // Optional feedback icon
  .form-control-feedback {
    color: $text-color;
  }
}

@mixin form-control-focus($border-color: $input-border-focus, $text-color: $input-text-focus, $bg-color: $input-bg-focus) {
  &:focus,
  &.btn:focus {
    outline: 0; // .form-control:focus outline is styled manually
    color: $text-color;
    border-color: $border-color;
    background-color: $bg-color;
  }
}
