[data-toggle='tooltip'] {
  cursor: pointer;
}

// .tolltip {
//   pointer-events: none;
// }

.tooltip-inner {
  padding: 12px 16px;
  font-weight: $font-weight-light;
  text-align: left;
  border-radius: 0;
  font-size: $font-size-small;
  box-shadow: $default-box-shadow;
  line-height: $tooltip-line-height;
}

// blue circular info icon
.tooltip-info {
  padding: 1px;
  font-size: 90%;
  line-height: 1;
  font-style: normal;
  font-weight: $font-weight-normal;
  &:before {
    font-family: FontAwesome;
    content: $fa-var-info-circle;
    color: $brand-info;
    .disabled & {
      color: $disabled-color;
    }
  }
  label & {
    float: right;
  }
  .control-label & {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.control-label {
  position: relative;
  padding-right: 18px;
}
