.table {
  @extend %bottom-gutter;
  ul, ol { margin-bottom: 0; }
  > thead,
  > tfoot {
    > tr {
      > th,
      > td {
        font-size: $font-size-small;
        line-height: 16px;
        color: $text-muted;
        font-weight: $font-weight-normal;
      }
    }
  }
  > thead {
    > tr {
      > th {
        border-bottom: 1px solid $table-outer-border-color;
      }
    }
  }
  > tbody {
    border-bottom: 1px solid $table-outer-border-color;
    border-top: 1px solid $table-outer-border-color;
    > tr:first-child {
      > * {
        border-top: none;
      }
    }
  }
  > tfoot {
    > tr {
      > td {
        padding: 12px 0;
        border-top: none;
      }
    }
  }
  td {
    font-size: $font-size-base;
    font-weight: $font-weight-normal;
  }
  th, td {
    &:first-child {
      padding-left: $panel-body-padding;
    }
    &:last-child {
      padding-right: $panel-body-padding;
    }
  }
}

.table-responsive {
  @extend %bottom-gutter;
  > .table { margin-bottom: 0; }
  // the below is taken from Bootstrap, but it was $screen-xs-max - but $screen-sm-max works better
  @media screen and (max-width: $screen-sm-max) {
    width: 100%;
    overflow-y: hidden;
    -ms-overflow-style: auto; // Bootstrap wants to hide the scrollbars in IE and Edge and it was causing some problems, particularly with jQuery-crollbars
    border: 1px solid $table-border-color;
    &.scrollbars > .table {
      margin-bottom: 12px; // added this gap for the custom scrollbar to slide
    }
    > .table {
      margin-bottom: -1px;
      > thead,
      > tbody,
      > tfoot {
        > tr {
          > th,
          > td {
            white-space: nowrap;
          }
        }
      }
      > tbody {
        border-top: none;
        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
}
