.accordion-menu {
  background: $menu-bg-color;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-top: 2px solid $menu-edge-color;
  letter-spacing: 0.02em;

  > .scrollbars {
    height: 100%;
  }

  ul.nav {

    border-bottom: 2px solid $menu-edge-color;
    padding: 2px 0;

    ul { padding: 0; }

    // menu items: all levels
    li {
      &.active > a {
        color: $menu-base-color;
        border-left: 4px solid $menu-link-color;
        background: $menu-line-color;
        z-index: 2; // z-index to cover the li:before and li:after lines
      }
      a {
        cursor: pointer;
        font-weight: $font-weight-light;
        font-size: $font-size-small;
        display: block;
        color: $menu-link-color;
        line-height: 1.25;
        padding: 8px 36px 8px 64px;
        border-left: 4px solid transparent;
        &:hover {
          text-decoration: none;
          background: $menu-accordion-hover-bg;
          z-index: 1; // z-index to cover the li:before and li:after lines
          &,
          &.next:before {
            color: $menu-base-color;
          }
        }
        // accordion
        &.next {
          position: relative;
          // caret
          &:before {
            color: $menu-link-color;
            @include fa-icon;
            content: $fa-var-angle-up;
            position: absolute;
            top: 50%;
            right: 26px;
            transform: translateY(-50%);
          }
          &.collapsed:before {
            content: $fa-var-angle-down;
          }
        }
      }
    }

    // menu items: 1st (top) level
    > li {
      &:after {
        content: '';
        border-bottom: 2px solid $menu-line-color;
        position: absolute;
        display: block;
        right: 16px;
        left: 16px;
        bottom: 0;
      }
      &:last-child:after {
        display: none;
      }
      > a {
        font-size: $font-size-base;
        padding: 16px 40px 18px 16px;
        // negative margin to cover the li:before and li:after lines
        margin: -2px 0;
      }

      // menu: 2nd level
      > ul {
        margin-top: 2px;
        // fake "margin-bottom" because accordion animation breaks with real margin/padding rules
        &:after {
          content: '';
          display: block;
          height: 22px;
        }
        li {
          a {
            padding-left: 32px;
          }
          // menu: 3rd level
          > ul {
            li {
              a {
                padding-left: 48px;
              }
              // menu: 4th level
              > ul {
                li {
                  a {
                    padding-left: 64px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
