.breadcrumb {
  font-family: $font-family-serif;
  font-style: italic;
  font-size: $font-size-xsmall;
  padding: 0 2px;
  margin-bottom: 8px;
  + h1, + h2, + h3, + h4, + h5 {
    margin-top: 0;
  }
  > li {
    + li:before {
      padding: 0 3px;
    }
    &.fa {
      font-size: 115%;
      margin-right: 7px;
      color: $breadcrumb-active-color;
      + li:before {
        display: none;
      }
    }
  }
}
