@mixin placeholder-selector {
  /* stylelint-disable */
  &::-webkit-input-placeholder { @content }
  &:-moz-placeholder { @content }
  &::-moz-placeholder { @content }
  &:-ms-input-placeholder { @content }
  &:input-placeholder { @content }
  /* styelint-enable */
  // autoprefixer automatically adds all these prefixes to the below after build, but for development purposes we'll use the above
  // &::placeholder { @content; }
}
