.file-picker {

  .file-uploader {
    .progress {
      height: 2px;
      margin-bottom: 8px;
      clear: both;
    }

    .upload-status {
      float: right;
      color: $gray;
      font-size: $font-size-small;
      margin-top: 4px;
    }
  }

  .file-details {
    line-height: 2;
    margin-bottom: 5px;

    [data-toggle='collapse'] {
      cursor: pointer;
      position: relative;
      font-weight: $font-weight-bold;
      &:before {
        @include fa-icon;
        content: $fa-var-angle-up;
        color: $brand-info;
        font-size: 2.3rem;
        position: absolute;
        top: 53%;
        right: 0;
        transform: translateY(-1.5rem);
      }
      &.collapsed:before {
        content: $fa-var-angle-down;
      }
    }

    .file-label {
      .close {
        font-size: 16px;
        margin: 0;
        width: 15px;
        height: 20px;
        &:before {
          @include fa-icon;
          content: $fa-var-trash;
        }
        &:hover {
          opacity: 1;
          color: $brand-info;
        }
      }
    }
  }

  .file-dropzone {
    overflow: hidden;
    position: relative;
    background: $body-bg;
    font-style: italic;
    border: 1px dashed $gray;
    border-radius: $input-border-radius;
    padding: ($vertical-gutter / 2) $vertical-gutter;
    width: 100%;
    color: $text-color;
    @include mobile-only {
      padding: 10px 20px;
    }
    &.active,
    &:hover {
      background: $btn-disabled-bg;
      a {
        text-decoration: underline;
      }
    }
    &.disabled {
      cursor: no-drop;
      background: $btn-disabled-bg;
      font-style: italic;
      &, a {
        color: $btn-disabled-color;
        text-decoration: none;
      }
      input[type='file'] {
        display: none;
      }
    }
    > .fa {
      float: left;
      height: 45px;
      line-height: 1.5;
      padding-right: 24px;
      font-size: 34px;
    }
    a {
      color: $brand-info;
      font-style: normal;
    }
    .one-line {
      line-height: 3;
    }
    input[type='file'] {
      opacity: 0;
      font-size: 118px;
      position: absolute;
      right: -100px;
      top: -70px;
      z-index: 1;
      padding: 0;
      margin: 0;
      cursor: pointer;
      filter: alpha(opacity=0);
      font-family: sans-serif;
      color: transparent;
    }
    .has-error & {
      border-color: $brand-danger;
    }
    .has-success & {
      border-color: $brand-success;
    }
    .has-warning & {
      border-color: $brand-warning;
    }
  }
}
