// show/hide icon (.form-group.has-password-toggle)
.has-password-toggle {
  position: relative;

  // padding (medium, small, large)
  .form-control {
    padding-right: $input-height-base;
  }

  .input-sm.form-control,
  .input-group-sm .form-control,
  &.input-group-sm .form-control,
  &.form-group-sm .form-control {
    padding-right: $input-height-small;
  }

  .input-lg.form-control,
  .input-group-lg .form-control,
  &.input-group-lg .form-control,
  &.form-group-lg .form-control {
    padding-right: $input-height-large;
  }

  // eyecon (medium, small, large)
  .password-toggle {
    cursor: pointer; // this is actually required for iOS to properly detect the click event
    font-family: FontAwesome;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    text-align: center;
    width: $input-height-base;
    height: $input-height-base;
    line-height: $input-height-base;
    font-size: $input-font-size-base;
    color: $gray;
    &:hover {
      color: $brand-info;
    }
    &:after {
      content: $fa-var-eye;
    }
    &.password-visible:after {
      content: $fa-var-eye-slash;
    }
  }

  label ~ .password-toggle {
    top: $font-size-base + $form-label-margin + 1px; // Height of the `label` + its margin + a border(?)
  }

  .input-sm ~ .password-toggle,
  .input-group-sm .password-toggle,
  &.input-group-sm .password-toggle,
  &.form-group-sm .password-toggle {
    width: $input-height-small;
    height: $input-height-small;
    line-height: $input-height-small;
    font-size: $font-size-small;
  }

  .input-lg ~ .password-toggle,
  .input-group-lg .password-toggle,
  &.input-group-lg .password-toggle,
  &.form-group-lg .password-toggle {
    width: $input-height-large;
    height: $input-height-large;
    line-height: $input-height-large;
    font-size: $font-size-large;
  }

  // this seems like a nasty hack, but it appears to allow for password toggles inside of input-groups with addons on the right (hasn't been battle tested)
  &.input-group .password-toggle,
  .input-group .password-toggle {
    position: relative;
    display: inline-block;
    width: 0;
    right: $font-size-base;
    padding: 0 (2 * $font-size-base - 2) 0 $font-size-base;
    margin: 0 (-2 * $font-size-base + 2) 0 -$font-size-base;
  }
  &.input-group-sm .password-toggle,
  .input-group-sm .password-toggle {
    right: $font-size-small;
    padding: 0 (2 * $font-size-small - 2) 0 $font-size-small;
    margin: 0 (-2 * $font-size-small + 2) 0 -$font-size-small;
  }
  &.input-group-lg .password-toggle,
  .input-group-lg .password-toggle {
    right: $font-size-large;
    padding: 0 (2 * $font-size-large - 2) 0 $font-size-large;
    margin: 0 (-2 * $font-size-large + 2) 0 -$font-size-large;
  }
}
