.pagination {
  margin-top: 0;
  > li {
    &.pagination-extra {
      padding: 8px 24px 8px 0;
      font-size: $font-size-small;
      color: $pagination-active-color;
    }
    > label {
      font-size: $font-size-small;
      color: $pagination-active-color;
      display: inline-block;
      padding-right: 8px;
    }
    > a,
    > span {
      float: none;
      display: inline-block;
      font-size: $font-size-small;
      > .fa {
        font-size: 24px;
        top: 3px;
        position: relative;
      }
    }
    > a {
      padding: 8px 11px;
    }
    > span {
      padding: 8px 0;
    }
    > select {
      display: inline-block;
      width: auto;
    }
  }
  > .active {
    > a,
    > span {
      font-weight: $font-weight-bold;
    }
  }
  > :not(.active) {
    > a {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
