// base button styles
.btn {
  letter-spacing: 0.01em;
  position: relative; // makes it easier to poistion icons and badges inside buttons
  color: $text-color;
  transition: $btn-transition;

  // medium button (and default)
  height: $input-height-base;
  &, .fa {
    line-height: 1.6;
  }

  .badge {
    margin-top: 5px;
    margin-bottom: 3px;
  }

  .icon-badge {
    margin-top: 0;
  }

  // x-small size button
  &.btn-xs {
    height: 30px;
    .badge {
      margin-top: 3px;
      margin-bottom: 3px;
    }
  }

  // small size button
  &.btn-sm,
  .input-group-sm &,
  .form-group-sm & {
    height: $input-height-small;
    .badge {
      margin-top: 4px;
    }
  }

  // large size button
  &.btn-lg,
  .input-group-lg &,
  .form-group-lg & {
    height: $input-height-large;
    .badge {
      margin-top: 7px;
      margin-bottom: 5px;
    }
  }

  // undo Bootstrap's oppionated button styles
  background: transparent;
  &:active,
  &.active {
    @include box-shadow(none);
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    @include opacity(1);
  }

  // generally everything inside a button should be consistently spaced (just use CSS overrides in special cases)
  > * {
    float: left;
    + * {
      margin-left: 10px;
    }
  }

  &.btn-block {
    display: flex; // IE10+ is the future!
    justify-content: center;
    /* Safari 7.1+ */
    _::-webkit-full-page-media, _:future, :root & { /* stylelint-disable-line */
      // Safari doesn't respect display: flex on buttons so it's best not to have any whitespace inside .btn-block because that will make the gaps bigger
      display: block;
    }
    > * {
      float: none;
    }
    > .text-left {
      order: -1;
    }
    > .text-right {
      order: 1;
    }
  }
}

// automatic spacing between items and text inside buttons with minial specificity
.btn { /* stylelint-disable-line */
  > .fa,
  > .badge,
  > .icon-badge {
    &:only-child {
      margin-right: 10px;
    }
  }
}

.btn-sq,
.btn-link,
.navbar-btn {
  > .fa,
  > .badge,
  > .icon-badge {
    &:only-child {
      margin-right: auto;
    }
  }
}

// existing button overrides
.btn-link {
  color: $link-color;
  letter-spacing: normal;
  &:hover {
    &,
    > span:not(.fa):not(.badge) {
      text-decoration: underline;
    }
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    font-style: italic;
    &, &:hover {
      color: $disabled-color;
    }
  }
  &:active {
    color: $link-active-color;
    text-decoration: underline;
  }
}

.btn-default {
  @include button-variant-states($btn-default-hover-color, $btn-default-hover-bg, $btn-default-active-bg);
  &:hover {
    border-color: white;
  }
}

.btn-primary {
  @include button-variant-states($btn-primary-hover-color, $btn-primary-hover-bg, $btn-primary-active-bg);
}

.btn-success {
  @include button-variant-states($btn-success-hover-color, $btn-success-hover-bg, $btn-success-active-bg);
}

.btn-info {
  @include button-variant-states($btn-info-hover-color, $btn-info-hover-bg, $btn-info-active-bg);
}

.btn-warning {
  @include button-variant-states($btn-warning-hover-color, $btn-warning-hover-bg, $btn-warning-active-bg);
}

.btn-danger {
  @include button-variant-states($btn-danger-hover-color, $btn-danger-hover-bg, $btn-danger-active-bg);
}

// new: secondary button
.btn-secondary {
  @include button-variant($btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);
  @include button-variant-states($btn-secondary-hover-color, $btn-secondary-hover-bg, $btn-secondary-active-bg);
}

// new: toggle button
.btn-toggle {
  border: none;
  &, .btn-group & {
    border-radius: 0;
  }
  &,
  &:focus {
    color: $btn-toggle-color;
  }
  &:hover,
  &:hover.active,
  &:hover.navbar-btn {
    color: $btn-toggle-color;
    background: $btn-toggle-hover-bg;
  }
  &:active,
  &.active {
    background: $btn-toggle-hover-bg;
    box-shadow: $btn-toggle-border 0 -2px 0 inset;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    box-shadow: none;
    color: $navbar-default-link-disabled-color;
    font-style: normal;
    &,
    &:hover {
      background: transparent;
    }
  }
  .btn-group & + & {
    margin-left: 1px;
  }
}

// new: square buttons (good for icons)
.btn-sq {
  padding-left: 2px;
  padding-right: 2px;
  text-align: center;
  width: $input-height-base;
  > * {
    float: none;
  }
  &.btn-xs {
    width: 30px;
  }
  &.btn-sm {
    width: $input-height-small;
  }
  &.btn-lg {
    width: $input-height-large;
  }
}
