// responsive gutters - these ensure that vertical gaps are always consistent sizes
@mixin responsive-vertical-gutter($property, $multiplier: 1, $important: '') {
  @if $property == 'margin' or $property == 'padding' {
    #{$property}-top: ($multiplier * 0.5 * $vertical-gutter) #{$important};
    #{$property}-bottom: ($multiplier * 0.5 * $vertical-gutter) #{$important};
    @include large-only {
      #{$property}-top: ($multiplier * $vertical-gutter) #{$important};
      #{$property}-bottom: ($multiplier * $vertical-gutter) #{$important};
    }
  }
  @else {
    #{$property}: ($multiplier * 0.5 * $vertical-gutter) #{$important};
    @include large-only {
      #{$property}: ($multiplier * $vertical-gutter) #{$important};
    }
  }
}

// convenience function to calculate multiples of the gutter size
@function gutter-unit($multiplier: 1) {
  @return $vertical-gutter / 10 * $multiplier;
}
