// tab styling
.nav-tabs {
  padding: 0 $grid-gutter-width-small;
  white-space: nowrap;
  display: table;
  width: 100%;
  > li {
    text-align: center;
    > a {
      border: none;
      display: inline-block;
      padding: 1px $grid-gutter-width-small;
      border-bottom: 2px solid transparent;
      min-height: 30px; // makes normal tab height consistent with progress-tab height
    }
    &.dropdown > a {
      padding-right: $input-height-base;
    }
  }
  &#{&} {
    border-bottom: 1px solid $gray-light;
    > li {
      a {
        margin: 0;
        border-radius: 0;
      }
      &.active {
        > a,
        > a:hover,
        > a:focus {
          border: none;
          border-bottom: 2px solid $nav-tabs-active-link-border-color;
        }
      }
      &.disabled {
        cursor: not-allowed;
        > a {
          font-style: italic;
          pointer-events: none;
        }
      }
    }
  }
  // tab justification
  &.nav-justified {
    > li {
      display: table-cell;
      width: auto;
    }
  }
  // tab centering
  &.nav-centered {
    text-align: center;
    > li {
      display: inline-block;
      float: none;
    }
  }
}

// responsive scrolling
.nav-tabs-responsive {
  overflow-x: auto;
  @include mobile-only {
    .nav-tabs {
      > li {
        float: none;
        display: inline-block;
      }
    }
  }
  &.scrollbars.scroll-scrollx_visible {
    padding-bottom: 16px; // added this gap for the custom scrollbar to slide
  }
  &.scrollbar-inner > .scroll-element.scroll-x {
    left: 0;
    right: 0;
  }
}

// tab content
.tab-content {
  margin: $grid-gutter-width-small ($grid-gutter-width-small / 2) 0 ($grid-gutter-width-small / 2);
}

// pills
.nav-pills {
  > li {
    > a {
      padding: 3px 8px;
    }
    &.dropdown {
      > a {
        padding-right: $input-height-base;
      }
    }
  }
}
