// Bootstrap tries to prevent body scrolling behind modals, but it leads to more bugs/problems so for now lets undo Bootstrap.
// see: https://github.com/twbs/bootstrap/issues/14040

body {
  padding-right: 0px !important; /* stylelint-disable-line */
  padding-left: 0px !important; /* stylelint-disable-line */
}

// default Bootstrap .modal-header .close
.modal-header .close {
  margin: -14px;
}

// vertically align center
.modal {
  text-align: center;
  padding: 0 !important; /* stylelint-disable-line */
  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .modal-dialog {
    max-width: 90%;
    display: inline-block;
    text-align: left;
    vertical-align: middle;
    &.modal-auto {
      width: auto;
    }
  }
}

// modal-content
.modal-content {
  border: none;
  box-shadow: none;
  border-radius: 0;
  // force small gutters inside all modals
  p {
    &:last-child {
      margin-bottom: -0.125em;
    }
  }
}

// modal header
.modal-header {
  .modal-title {
    margin-top: -0.125em;
    margin-bottom: -0.125em;
    font-weight: $font-weight-bold;
  }
  + .modal-body,
  + .modal-footer {
    padding-top: 0;
  }
}

// modal body
.modal-body {
  + .modal-footer {
    padding-top: 0;
  }
}

// modal message (large text message)
.modal-message {
  font-size: $font-size-large-h4;
  font-weight: $font-weight-light;
  display: inline-block;
  vertical-align: middle;
  margin: 0 18px;
}

// alert-modal class (for styling system generated alert modals from modal.js)
.alert-modal {
  line-height: $line-height-small;
}

// full-modal style (100% height, slides in from the right)
.full-modal {

  // slide in from the right (instead of top)
  &.fade:not(.in) .modal-dialog {
    transform: translate(25%, 0);
  }

  // size and position
  .modal-dialog {
    margin: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: none;
    @media (min-width: $modal-full-breakpoint) {
      max-width: $modal-full-width-sm;
    }
    .modal-content {
      height: 100%;
      background: $popup-bg-color;
      overflow-y: auto;
      overflow-x: hidden;
    }
  }

  // modal header
  .modal-header {
    position: relative;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: $vertical-gutter/2;
    .modal-title {
      padding: 20px 20px 20px 0;
      min-height: $navbar-height;
      font-weight: $font-weight-light;
      color: $popup-text-color;
      .fa:first-child {
        margin-right: 0.4rem;
      }
    }
    .close {
      color: $popup-link-color;
      margin: 6px -18px;
    }
    &:after {
      content: '';
      border-bottom: 2px solid $popup-line-color;
      display: block;
      position: absolute;
      left: $modal-inner-padding;
      right: 0;
      bottom: 0;
    }
  }

  // modal body
  .modal-body {
    position: absolute;
    top: $navbar-height;
    overflow-y: auto;
    padding-top: 0;
    // padding-bottom: 0;
    width: 100%;
    bottom: 105px;
    &:last-child {
      bottom: 0;
    }
    // for scrollbars to function
    &.scrollbars {
      position: absolute;
      top: $navbar-height;
      bottom: 0;
      left: 0;
      right: 0;
      > .modal-body {
        top: 0;
        padding: 0 $modal-inner-padding;

        /* stylelint-disable */
        // jQueryScrollbar plugin sets some !important and inline styles that we have to over-ride for the scrollbars to render nicely in all major browsers.
        overflow-x: hidden !important;
        height: 100% !important;
        /* stylelint-enable */

        // create a consistent margin-bottom (incase it is desired), which can be overriden if not desired
        &:after {
          content: '';
          margin-top: 20px;
          display: block;
        }
      }
    }
    // styled elements
    @include all-headings {
      margin: 26px 0 20px 0;
      font-weight: $font-weight-bold;
      color: $popup-text-color;
    }
    label {
      color: $popup-text-color;
    }
    p {
      color: $popup-text-color;
      margin-bottom: $vertical-gutter/2;
    }
    > p {
      &:first-child {
        margin-top: 20px;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    hr {
      border-color: $popup-line-color;
      margin: $vertical-gutter/2 0;
    }
  }

  .modal-footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    &:before {
      content: '';
      width: 100%;
      height: $modal-inner-padding;
      border-top: 2px solid $popup-line-color;
    }
  }
}
