// new mixin just for hover colors - it's a seperate mixin because it's the same for most (but not all) buttons
@mixin button-variant-states($hover-color: $btn-default-hover-color, $hover-background: $btn-default-hover-bg, $active-background: $btn-default-active-bg) {
  &:hover {
    color: $hover-color;
    background-color: $hover-background;
    border-color: transparent;
  }
  &:active {
    color: white;
    background-color: $active-background;
  }
}

// replace Bootstrap's button-generator mixin with a better one
@mixin button-variant($color, $background, $border) {
  &:not(:hover) {
    background-color: $background;
    color: $color;
    border-color: $border;
  }

  &.disabled, &[disabled], fieldset[disabled] & {
    &, &:hover, &:focus, &.focus {
      font-style: italic;
      color: $btn-disabled-color;
      border-color: transparent;
      background-color: $btn-disabled-bg;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}

@mixin navbar-btn-active {
  background: $btn-toggle-hover-bg;
  color: $navbar-default-link-disabled-color;
  box-shadow: $navbar-default-link-disabled-color 0 -2px 0 inset;
}
