[data-toggle='popover'] {
  cursor: pointer;
}

.popover {
  padding: 12px 16px;
  border-radius: 0;
  border: none;
  box-shadow: $default-box-shadow;
  outline: none;
  > .arrow:after {
    border-width: $popover-arrow-width + 1; // overlap arrows by 1px to prevent possible gap appearing on some devices
  }
  .popover-content,
  .popover-title,
  .btn-link {
    color: $popover-color;
  }
  .popover-title {
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    border-color: transparent;
    padding: 0 0 5px 0;
  }
  .popover-content {
    line-height: $tooltip-line-height;
    font-size: $font-size-small;
    font-weight: $font-weight-light;
    padding: 0;
    .btn-link-toolbar {
      margin: -12px -16px;
      .btn {
        min-height: auto;
        font-size: $font-size-small;
        font-weight: $font-weight-light;
      }
      .btn-separator {
        background: rgba($popover-color, 0.15);
      }
    }
  }
}
