@import './_paths-relative.scss';

// ========== Theme ========== //
$theme:                                 'light' !default;
$brand-theme:                           'dd' !default;


// ========== Colour ========== //
// ---------- Brand colours ---------- //
$brand-primary:                       #72bf44 !default;
$brand-primary-dark:                  #72bf44 !default;
$brand-white:                         #ffffff !default;
$brand-black:                         #000000 !default;
$brand-secondary:                     #455565 !default;
// ---------- Action colours ---------- //
$brand-info:                          #0784c1 !default;
$brand-success:                       #2e7d32 !default;
$brand-warning:                       #f5a81c !default;
$brand-danger:                        #cd202e !default;
// ---------- Grays ---------- //
$gray-base:                           #050505 !default;
$gray-darkest:                        #232323 !default;
$gray-darker:                         #565656 !default;
$gray-dark:                           #8c8c8c !default;
$gray:                                #a9a9a9 !default;
$gray-light:                          #cccccc !default;
$gray-lighter:                        #eaebea !default;
$gray-lightest:                       #f9f9f9 !default;
// ---------- Complementary colours ---------- //
$brand-complementary-1:               #27aae1 !default;
$brand-complementary-2:               #437cb1 !default;
$brand-complementary-3:               #189ba9 !default;
$brand-complementary-4:               #00b39f !default;
$brand-complementary-5:               #c3d52e !default;
$brand-complementary-6:               #ffffff !default;
$brand-complementary-7:               #ffffff !default;
$brand-complementary-8:               #ffffff !default;
// ---------- State colours ---------- //
// Default disabled
$disabled-color:                        $gray-light !default;
// Global color for active items (e.g., navs or dropdowns).
$component-active-color:              #fff !default;
// Global background color for active items (e.g., navs or dropdowns).
$component-active-bg:                   $brand-info !default;
// ---------- Components ---------- //
// ---------- Body
// Background color for body
$body-bg:                             white !default;
// Global text color on body
$text-color:                            $brand-secondary !default;
// ---------- Footer
$footer-bg:                             $gray-lighter !default;
// ---------- Links
// Global textual link color
$link-color:                            $brand-info !default;
$link-hover-color:                      $brand-info !default;
$link-active-color:                     $brand-secondary !default;
// ---------- Wells
$well-bg:                               $gray-lightest !default;
$well-border:                           $gray-lighter !default;
$well-color:                            $text-color !default;
// ---------- Menus
$menu-bg-color:                         $brand-secondary !default;
$menu-base-color:                     white !default;
$menu-line-color:                       mix($menu-base-color, $menu-bg-color, 15%) !default;
$menu-edge-color:                       mix($menu-base-color, $menu-bg-color, 30%) !default;
$menu-link-color:                       mix($menu-base-color, $menu-bg-color, 70%) !default;
// Accordion menu hover colour
$menu-accordion-hover-bg:               $brand-info !default;
// Theme alt menu colours
$menu-alt-bg-color:                   #616f7c !default;
// ---------- Pop-overs, modals & tooltips
// Pop-overs
$popup-bg-color:                      #6a7784 !default;
$popup-line-color:                      rgba($menu-base-color, 0.25) !default;
$popup-link-color:                      rgba($menu-base-color, 0.7) !default;
$popup-text-color:                      rgba($menu-base-color, 0.9) !default;
// Modals
$modal-content-border-color:            transparent !default;
$modal-content-fallback-border-color:   transparent !default;
$modal-header-border-color:             transparent !default;
// Tooltips
$tooltip-color:                       #fff !default;
$tooltip-bg:                            $popup-bg-color !default;
// TODO fix this issue
// ---------- Pop-overs
$popover-bg:                            $tooltip-bg !default;
$popover-color:                         $tooltip-color !default;
$popover-border-color:                  transparent !default;
$popover-fallback-border-color:         transparent !default;
$popover-title-bg:                      transparent !default;
// ---------- Form elements
// Inputs
$input-bg:                              $body-bg !default;
$input-bg-disabled:                     $input-bg !default;
$input-bg-focus:                        $body-bg !default;
$input-color:                           $gray-darker !default;
$input-color-placeholder:               $gray-light !default;
$input-group-addon-color:               $text-color !default;
$input-text-focus:                      $brand-info !default;
$input-border:                          $gray-light !default;
$input-border-focus:                    $brand-info !default;
// Drop-downs
$dropdown-bg:                           $input-bg-focus !default;
$dropdown-divider-bg:                   $input-border !default;
$dropdown-link-color:                   $gray-darker !default;
$dropdown-link-hover-color:             $brand-info !default;
$dropdown-link-hover-bg:                transparent !default;
// Buttons
// Default
$btn-default-color:                     $brand-info !default;
$btn-default-bg:                        $body-bg !default;
$btn-default-border:                    $brand-info !default;
$btn-default-hover-color:               $brand-secondary !default;
$btn-default-hover-bg:                #c8ccd1 !default;
$btn-default-active-bg:               #a9a9a9 !default;
// Primary
$btn-primary-border:                    transparent !default;
$btn-primary-hover-color:               $brand-secondary !default;
$btn-primary-hover-bg:                #a3d585 !default;
$btn-primary-active-bg:               #5d9936 !default;
// Secondary
$btn-secondary-color:                 white !default;
$btn-secondary-bg:                      $brand-secondary !default;
$btn-secondary-border:                  transparent !default;
$btn-secondary-hover-color:             $brand-secondary !default;
$btn-secondary-hover-bg:              #c8ccd1 !default;
$btn-secondary-active-bg:             #34404c !default;
// Toggle
$btn-toggle-color:                      $text-color !default;
$btn-toggle-bg:                         transparent !default;
$btn-toggle-border:                     $brand-primary !default;
$btn-toggle-hover-color:                $text-color !default;
$btn-toggle-hover-bg:                   $gray-lightest !default;
// Info
$btn-info-border:                       transparent !default;
$btn-info-hover-color:                  $brand-secondary !default;
$btn-info-hover-bg:                   #b5daed !default;
$btn-info-active-bg:                  #056391 !default;
// Success
$btn-success-border:                    transparent !default;
$btn-success-hover-color:               $brand-secondary !default;
$btn-success-hover-bg:                #ccdab8 !default;
$btn-success-active-bg:               #3e620c !default;
// Warning
$btn-warning-color:                     $gray-darkest !default;
$btn-warning-border:                    transparent !default;
$btn-warning-hover-color:               $brand-secondary !default;
$btn-warning-hover-bg:                #fee8ba !default;
$btn-warning-active-bg:               #bb8511 !default;
// Danger
$btn-danger-border:                     transparent !default;
$btn-danger-hover-color:                $brand-secondary !default;
$btn-danger-hover-bg:                 #f7b8b8 !default;
$btn-danger-active-bg:                #ac0c0c !default;
// Disabled
$btn-link-disabled-color:               $disabled-color !default;
$btn-disabled-color:                    $gray-light !default;
$btn-disabled-bg:                       $gray-lightest !default;
// Labels
$form-label-color:                      $text-color !default;
$label-default-bg:                      $btn-default-bg !default; //$gray-light
$label-secondary-bg:                    $brand-secondary !default;
$label-close-bg:                      rgba(0, 0, 0, 0.2) !default;
$label-bg:                              $gray-lighter !default; //$label-bg
$label-color:                           $text-color !default; //#fff
$label-warning-color:                   $gray-darkest !default;
$label-close-color:                     $brand-info !default;
// ---------- Headings
$headings-color:                        $brand-secondary !default;
// ---------- Lists
$ol-bullet-bg:                          $text-color !default;
$ol-bullet-color:                       $body-bg !default;
// ---------- Tables
// Background color used for `.table-striped`
$table-bg-accent:                       $gray-lightest !default;
// Background color used for `.table-hover`
$table-bg-hover:                        rgba($gray-lighter, 0.666) !default;
// Border color for table and cell borders
$table-border-color:                    $gray-lighter !default;
$table-outer-border-color:              $gray-light !default;
// ---------- Navbar
$navbar-default-color:                  $gray-darker !default;
$navbar-default-bg:                     $body-bg !default;
$navbar-default-border:                 $menu-link-color !default;
$navbar-default-divider:                $gray-lighter !default;
// Navbar links
$navbar-default-link-color:             $gray-darker !default;
$navbar-default-link-hover-color:       $brand-info !default;
$navbar-default-link-active-color:      $brand-info !default;
$navbar-default-link-active-bg:         transparent !default;
$navbar-default-link-disabled-color:    $gray !default;
// ---------- Nav
// Nav link
$nav-link-hover-bg:                     transparent !default;
// Tabs
$nav-tabs-border-color:                         transparent !default;
$nav-tabs-link-hover-border-color:              transparent !default;
$nav-tabs-active-link-hover-bg:                 transparent !default;
$nav-tabs-active-link-hover-color:              $brand-secondary !default;
$nav-tabs-active-link-hover-border-color:       transparent !default;
$nav-tabs-justified-link-border-color:          transparent !default;
$nav-tabs-justified-active-link-border-color:   $brand-primary !default;
$nav-tabs-active-link-border-color:             $brand-primary !default;
// ---------- Pagination
// Base
$pagination-color:                      $brand-info !default;
$pagination-bg:                         transparent !default;
$pagination-border:                     transparent !default;
// Hover
$pagination-hover-bg:                   transparent !default;
$pagination-hover-border:               transparent !default;
// Active
$pagination-active-color:               $gray !default;
$pagination-active-bg:                  transparent !default;
$pagination-active-border:              transparent !default;
// Disabled
$pagination-disabled-color:             $disabled-color !default;
$pagination-disabled-bg:                transparent !default;
$pagination-disabled-border:            transparent !default;
// ---------- Jumbotron
$jumbotron-color:                       $brand-secondary !default;
$jumbotron-bg:                          $gray-lighter !default;
$jumbotron-heading-color:               inherit !default;
// ---------- Alerts
// State success
$state-success-text:                    $brand-success !default;
$state-success-bg:                      mix($brand-success, $body-bg, 10%) !default;
$state-success-border:                  transparent !default;
// State info
$state-info-text:                       $brand-info !default;
$state-info-bg:                         mix($brand-info, $body-bg, 10%) !default;
$state-info-border:                     transparent !default;
// State warning
$state-warning-text:                    $brand-warning !default;
$state-warning-bg:                      mix($brand-warning, $body-bg, 10%) !default;
$state-warning-border:                  transparent !default;
// State danger
$state-danger-text:                     $brand-danger !default;
$state-danger-bg:                       mix($brand-danger, $body-bg, 10%) !default;
$state-danger-border:                   transparent !default;
// Success
$alert-success-bg:                      $brand-success !default;
$alert-success-text:                  white !default;
$alert-success-border:                  $brand-success !default;
// Info
$alert-info-bg:                         $brand-info !default;
$alert-info-text:                     white !default;
$alert-info-border:                     $brand-info !default;
// Warning
$alert-warning-bg:                      $brand-warning !default;
$alert-warning-text:                  white !default;
$alert-warning-border:                  $brand-warning !default;
// Danger
$alert-danger-bg:                       $brand-danger !default;
$alert-danger-text:                   white !default;
$alert-danger-border:                   $brand-danger !default;
// ---------- Progress bars
$progress-bg:                           $gray-lighter !default;
// ---------- List groups
$list-group-bg:                         $body-bg !default;
$list-group-border:                     $well-border !default;
$list-group-hover-bg:                   $well-bg !default;
$list-group-active-text-color:          $well-bg !default;
$list-group-link-color:                 $text-color !default;
$list-group-link-heading-color:         $headings-color !default;
// ---------- Panels
$panel-bg:                              $body-bg !default;
$panel-closed-bg:                       $gray-lightest !default;
$panel-body-padding:                    30px !default;
$panel-heading-padding:                 $panel-body-padding !default;
$panel-border-radius:                   0 !default;
$panel-footer-bg:                       $panel-bg !default;
$panel-default-border:                  $gray-lighter !default;
$panel-primary-border:                  $brand-primary !default;
$panel-success-border:                  $brand-success !default;
$panel-info-border:                     $brand-info !default;
$panel-warning-border:                  $brand-warning !default;
$panel-danger-border:                   $brand-danger !default;
$panel-secondary-border:                $brand-secondary !default;
// ---------- Badges
// Linked badge text color on hover
$badge-bg:                              $brand-info !default;
// ---------- Breadcrumbs
$breadcrumb-bg:                         transparent !default;
$breadcrumb-color:                      $btn-disabled-color !default;
$breadcrumb-active-color:               $btn-disabled-color !default;
// ---------- Close icon
$close-color:                           rgba($gray-dark, 0.9) !default;
// ---------- Horizontal rule
$hr-border:                             $gray-lighter !default;
// ---------- Code
$code-color:                            $well-color !default;
$code-bg:                               $well-bg !default;
$code-block-bg:                         $well-bg !default;
$pre-border-color:                      $well-border !default;
// ---------- Utilities ---------- //
// Text muted
$text-muted:                            $gray !default;
// ---------- Miscelaneous other defaults/globals colours ---------- //
// ---------- Box shadows
$default-box-shadow:                    1px 1px 0 0 rgba($gray-dark, 0.25) !default;
$default-box-shadow-up:                 2px 0 0 -1px rgba($gray-dark, 0.25) !default;
$panel-box-shadow:                      -1px -1px 0 0 $panel-default-border, 0 1px 0 1px $panel-default-border, 1px 0 0 1px $panel-default-border, 1px 1px 0 1px $panel-default-border !default;
// ---------- Text shadows
// Close icon
$close-text-shadow:                     none !default;
// ---------- Opacity
$tooltip-opacity:                       1 !default;
$modal-backdrop-opacity:                0.666 !default;


// ========== Background images ========== //
// ---------- Logos ---------- //
$logo-icon:                             '#{$img-base-path}icon.svg' !default;
$logo-horizontal:                       '#{$img-base-path}logo.svg' !default;
$logo-icon-white:                       '#{$img-base-path}icon-white.svg' !default;
$logo-horizontal-white:                 '#{$img-base-path}logo-white.svg' !default;
$logo-icon-navbar:                      $logo-icon !default;
$logo-horizontal-navbar:                $logo-horizontal !default;
// ---------- Components ---------- //
// Jumbotron
$jumbotron-jumbo-bg-img:                '#{$img-base-path}jumbo-bg.jpg' !default;
$jumbotron-jumbo-bg:                    lighten($brand-secondary, 2%) url($jumbotron-jumbo-bg-img) no-repeat 50% 50% / cover !default;


// ========== Typography ========== //
// ---------- Font stack ---------- //
// Default
$font-family-sans-serif:                'Helvetica Neue', helvetica, arial, sans-serif !default;
$font-family-serif:                     'Georgia', 'Times New Roman', 'Times', serif !default;
$font-family-brand:                     'Source Sans Pro', sans-serif !default;
$font-family-base:                      $font-family-brand !default;
// Default monospace stack
$font-family-monospace:                 'Menlo', 'Monaco, Consolas', 'Courier New', monospace !default;
// ---------- Line height ---------- //
// Unit-less line-height for use in components like buttons
$line-height-base:                      1.5 !default;
// Line height modifiers
$line-height-small:                     $line-height-base !default;
// Forms
$input-line-height:                     1.5 !default;
// ---------- Components
// Headings
$headings-line-height:                  1.1 !default;
// Tooltips
$tooltip-line-height:                   1.3 !default;
// Modals
$modal-title-line-height:               $headings-line-height !default;
// ---------- Font-weight ---------- //
$font-weight-light:                     300 !default;
$font-weight-normal:                    400 !default;
$font-weight-bold:                      700 !default;
// ---------- Components
// Headings
$headings-font-weight:                  $font-weight-light !default;
// Buttons
$btn-font-weight:                       $font-weight-normal !default;
// Badges
$badge-font-weight:                     $font-weight-normal !default;
// Close icon
$close-font-weight:                     normal !default;
// ---------- Decoration ---------- //
// ---------- Components
// Links
$link-hover-decoration:                 none !default;
// ---------- Font size ---------- //
// Base
$font-size-base:                        16px !default;
// Modifiers
$font-size-large:                       18px !default;
$font-size-small:                       14px !default;
$font-size-xsmall:                      12px !default;
// ---------- Components
// Headings
$font-size-h1:                          36px !default;
$font-size-h2:                          30px !default;
$font-size-h3:                          26px !default;
$font-size-h4:                          22px !default;
$font-size-h5:                          18px !default;
$font-size-h6:                          16px !default;
// Headings for larger screens
$font-size-large-h1:                    44px !default;
$font-size-large-h2:                    36px !default;
$font-size-large-h3:                    28px !default;
$font-size-large-h4:                    22px !default;
$font-size-large-h5:                    18px !default;
$font-size-large-h6:                    16px !default;
// Forms
$input-font-size-base:                  $font-size-small !default;
// Jumbotron
$jumbotron-font-size:                   $font-size-base !default;
$jumbotron-heading-font-size:           $font-size-large-h1 !default;
$jumbotron-text-weight:                 $font-weight-normal;
// Blockquote
$blockquote-font-size:                  $font-size-base !default;

// ========== Box model ========== //
// ---------- Unitless and/or property agnostic ---------- //
// Vertical gutter is used in a number of places and can be used as margin or padding
$vertical-gutter:                       40px !default;
// ---------- Margin ---------- //
// ---------- Components
// Forms
$form-label-margin:                     10px !default;
$form-group-margin-bottom:              25px !default;
// ---------- Padding ---------- //
// Base
$padding-base-horizontal:               16px !default;
$padding-base-vertical:                 8px !default;
// Modifiers
$padding-xs-horizontal:                 10px !default;
$padding-xs-vertical:                   2px !default;
$padding-small-horizontal:              12px !default;
$padding-small-vertical:                4px !default;
$padding-large-horizontal:              16px !default;
$padding-large-vertical:                10px !default;
// ---------- Components
// Navbar
$navbar-padding-horizontal:             5px !default;
$navbar-padding-vertical:               17px !default;
// Nav
$nav-link-padding:                      18px $navbar-padding-horizontal !default;
// Jumbotron
$jumbotron-padding:                     $vertical-gutter !default;
// Modals
$modal-inner-padding:                   30px !default;
$modal-title-padding:                   30px !default;
// Alerts
$alert-padding:                         8px !default;
// Breadcrumbs
$breadcrumb-padding-vertical:           0 !default;
$breadcrumb-padding-horizontal:         0 !default;
// ---------- Height & width ---------- //
// ---------- Navbar
// Logo horizontal
$logo-horizontal-height:                24px !default;
$logo-horizontal-width:                 104.25px !default;
// ---------- Forms
$input-height-large:                    50px !default;
$input-height-base:                     44px !default;
$input-height-small:                    35px !default;
// ---------- Navbar
$navbar-height:                         60px !default;
// ---------- Tooltips
$tooltip-max-width:                     170px !default;
// ---------- Pop-overs
$popover-max-width:                     425px !default;
$popover-arrow-width:                   5px !default;
// Modals
$modal-md:                              500px !default;
// Desktop & mobile menu components
$desktop-menu-width:                    250px !default;
$mobile-menu-width:                     350px !default;
$mobile-menu-width-xsmall:              273px !default;
// ---------- Border radius ---------- //
// ---------- Base
$border-radius-base:                    4px !default;
// ---------- Modifiers
$border-radius-small:                   $border-radius-base !default;
$border-radius-large:                   $border-radius-base !default;
// ---------- Components
// Navbar
$navbar-border-radius:                  0 !default;

// ---------- Z-index ---------- //
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.
//
// Note: These variables are not generated into the Customizer.
$zindex-popover:                        800 !default;
$zindex-dropdown:                       900 !default;
$zindex-outdated-browser:               1000 !default;
$zindex-navbar-fixed:                   1020 !default;
$zindex-menu:                           1050 !default;
$zindex-modal-background:               1060 !default;
$zindex-modal:                          1070 !default;
$zindex-toast:                          1080 !default;
$zindex-site-search:                    1090 !default;
$zindex-tooltip:                        1100 !default;


// ========== Components modifiers & other values ========== //
// ---------- Transitions ---------- //
$default-transition:                    0.25s ease-out !default;
// ---------- Tables ---------- //
// Padding for table headings and cells
$table-cell-padding:                    12px !default;
// ---------- Buttons ---------- //
// Buttons transition
$btn-transition:                        background 0.1s linear, border 0.1s linear, color 0.1s linear !default;


// ========== Breakpoints ========== //
// Define the breakpoints at which your layout will change, adapting to different screen sizes.
// ---------- Extra extra large (standard desktops, TVs, etc) ---------- //
$screen-xxl:                            1200px !default;
$screen-xxl-min:                        $screen-xxl !default;
$screen-xxl-desktop:                    $screen-xxl-min !default;
// ---------- Extra large (small desktop) ---------- //
$screen-xl:                             1024px !default;
$screen-xl-min:                         $screen-xl !default;
$screen-xl-max:                         ($screen-xxl-min - 1) !default;
$screen-xl-desktop:                     $screen-xl-min !default;
// ---------- Large (big tablet) ---------- //
$screen-lg:                             960px !default;
$screen-lg-min:                         $screen-lg !default;
$screen-lg-max:                         ($screen-xl-min - 1) !default;
$screen-lg-desktop:                     $screen-lg-min !default;
// ---------- Medium (tablet) ---------- //
$screen-md:                             768px !default;
$screen-md-min:                         $screen-md !default;
$screen-md-max:                         ($screen-lg-min - 1) !default;
$screen-desktop:                        $screen-md-min !default;
// ---------- Small (phablet & tablet) ---------- //
$screen-sm:                             480px !default;
$screen-sm-min:                         $screen-sm !default;
$screen-sm-max:                         ($screen-md-min - 1) !default;
$screen-tablet:                         $screen-sm-min !default;
// ---------- Extra small (phone) ---------- //
$screen-xs:                             0 !default;
$screen-xs-min:                         $screen-xs !default;
$screen-xs-max:                         ($screen-sm-min - 1) !default;
$screen-phone:                          $screen-xs-min !default;
// ---------- Components ---------- //
// ---------- Mobile & desktop page lay-out
$desktop-breakpoint:                    $screen-xxl !default;
$desktop-breakpoint-max:                $desktop-breakpoint - 1 !default;
// ---------- Menu push & toggle
// On smaller screens push the site off the screen instead of squashing it to reduce complex design implications around break points
$desktop-menu-push-breakpoint:          $screen-xl-max + $desktop-menu-width !default;
$desktop-menu-push-breakpoint-max:      $desktop-menu-push-breakpoint - 1 !default;
$desktop-menu-toggle-width:             $navbar-height !default;
// ---------- Modals
// Full modal variables
$modal-full-breakpoint:                 600px !default;
$modal-full-width-sm:                   420px !default;
// ---------- Lists
// Point at which .dl-horizontal becomes horizontal
$dl-horizontal-breakpoint:              $screen-md-min !default;

// ========== Grid system ========== //
// ---------- Grid base values ---------- //
// Number of columns in the grid.
$grid-columns:                          12 !default;
// Padding between columns. Gets divided in half for the left and right.
$grid-gutter-width:                     32px !default;
$grid-gutter-width-small:               16px !default;
$grid-gutter-width-xsmall:              10px !default;
// At this breakpoint all gutters get narrower/smaller for smaller screens
$grid-gutter-breakpoint:                $screen-md !default;
$grid-gutter-breakpoint:                $screen-xxl !default;
$grid-gutter-breakpoint-max:            $grid-gutter-breakpoint - 1 !default;
// ---------- Grid collapse
// Point at which the navbar becomes uncollapsed.
// We set this to 0 to completely disable navbar collapsing. This allows us to still use dropdowns on smaller screens, and since the traditional navbar mobile menu is not part of the theme we don't really need collapsing functionality. It would trivial to add a 'slide-down' mobile menu to the theme if it is ever required.
$grid-float-breakpoint:                 0 !default;
// Point at which the navbar begins collapsing.
$grid-float-breakpoint-max:             0 !default;
// ---------- Container sizes
// Define the maximum width of `.container` for different screen sizes.
// Large screen / wide desktop
// (1140px + $grid-gutter-width)
$container-large-desktop:               ($screen-lg - $grid-gutter-width) !default;
// Medium screen / desktop
// (940px + $grid-gutter-width)
$container-desktop:                     ($screen-md - $grid-gutter-width) !default;
// Small screen / tablet
// (720px + $grid-gutter-width)
$container-tablet:                      ($screen-sm - $grid-gutter-width) !default;
