.desktop-menu-layout {
  transition: right $default-transition, margin-right $default-transition;
  position: relative;
  right: 0;
  .desktop-menu {
    transition: left $default-transition;
    position: fixed;
    top: $navbar-height - 2px;
    bottom: 0;
    left: - $desktop-menu-width;
    width: $desktop-menu-width;
    z-index: $zindex-menu;
  }
  &:after { @include menu-backdrop; }
}

//menu open on tablet (slide off screen with backdrop like mobile)
@media (min-width: $desktop-breakpoint) and (max-width: $desktop-menu-push-breakpoint-max) {
  .tablet-menu-open {
    .navbar .navbar-nav.navbar-center {
      margin-left: ($desktop-menu-width / 2);
    }
    .outdated-browser {
      left: $desktop-menu-width;
    }
    .desktop-menu-layout {
      right: - $desktop-menu-width;
      .navbar-fixed-top {
        right: - $desktop-menu-width;
      }
      .desktop-menu {
        left: 0;
      }
      &:after { @include menu-backdrop-visible; }
    }
  }
}

//show menu open on large desktop (squash inside screen)
@media (min-width: $desktop-menu-push-breakpoint) {
  .desktop-menu-open {
    .navbar .navbar-nav.navbar-center {
      margin-left: ($desktop-menu-width / 2);
    }
    .outdated-browser {
      left: $desktop-menu-width;
    }
    .desktop-menu-layout {
      right: - $desktop-menu-width;
      margin-right: $desktop-menu-width;
      .navbar-fixed-top {
        left: 0;
      }
      .desktop-menu {
        left: 0;
      }
    }
  }
}

// desktop desktop-menu toggle button
.desktop-menu-toggle {
  transition: width $default-transition;
  width: $desktop-menu-toggle-width;
  background: $menu-bg-color;
  cursor: pointer;
  overflow: hidden;
  .navbar .navbar-nav > & {
    padding: 0;
    a {
      padding: 0 20px;
      line-height: $navbar-height - 1px;
      height: $navbar-height;
      &,
      &:focus {
        color: $menu-link-color;
      }
      &:hover {
        color: $menu-base-color;
      }
    }
  }
  .hidden-opened {
    display: inherit;
    &:before {
      text-align: center;
      @include fa-icon;
      content: $fa-var-bars;
      width: 20px;
      font-size: 21px;
      position: relative;
      top: 3px;
    }
  }
  .hidden-closed {
    display: none;
    font-style: italic;
    font-family: $font-family-serif;
    font-size: $font-size-small;
    @include bc-close;
    &:before {
      font-size: 38px;
      margin-right: 9px;
      vertical-align: middle;
      position: relative;
      top: -3px;
      left: -2px;
    }
  }

  //show menu open on tablet
  @media (min-width: $desktop-breakpoint) and (max-width: $desktop-menu-push-breakpoint-max) {
    .tablet-menu-open & {
      width: $desktop-menu-width;
      .hidden-opened {
        display: none;
      }
      .hidden-closed {
        display: inherit;
      }
    }
  }

  //show menu open on large screen
  @media (min-width: $desktop-menu-push-breakpoint) {
    .desktop-menu-open & {
      width: $desktop-menu-width;
      .hidden-opened {
        display: none;
      }
      .hidden-closed {
        display: inherit;
      }
    }
  }
}
