.navbar {
  background-color: $navbar-default-bg;
  border: none;
  // unobtrusive way to add a border
  box-shadow: inset 0 -1px 0 $navbar-default-border, inset 0 1px 0 $navbar-default-border, inset -1px 0 0 $navbar-default-border, inset 1px 0 0 $navbar-default-border;
  // default navbar font styling
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-normal;
  font-size: $font-size-xsmall;
  line-height: 18px;
  letter-spacing: 0.01em;

  // site header's navbar
  &.navbar-fixed-top {
    box-shadow: inset 0 -1px 0 $navbar-default-border;
    transition: all $default-transition;
    transform: translate(0, 0);
    top: 0;
    left: 0;
    right: 0;
  }

  // navbar's dropdown caret style
  .caret {
    position: relative;
    height: 18px;
    width: 12px;
    top: -6px;
    left: 2px;
    margin-left: 0;
    // increase specificity to apply to dropUPs as well
    &.caret {
      border: none;
    }
    &:before {
      @include fa-icon;
      content: $fa-var-angle-down;
      color: $brand-info;
      font-size: 17px;
      vertical-align: middle;
    }
  }

  // caret pointing up
  .dropup .caret:before,
  .dropdown.open .caret:before {
    content: $fa-var-angle-up;
  }

  // caret pointing down
  .dropdown .caret:before,
  .dropup.open .caret:before {
    content: $fa-var-angle-down;
  }

  // navbar's dropdown-menu style
  .dropdown-menu {
    font-family: $font-family-brand;
    letter-spacing: normal;
    background: $navbar-default-bg;
    // dropdown-menu options should align with the dropdown button's text
    li > a {
      white-space: nowrap;
      padding: 4px 32px;
    }
  }

  // dropDOWN menu
  &:not(.navbar-fixed-bottom) .dropdown > .dropdown-menu {
    margin-top: -2px;
    border-top: 2px solid $navbar-default-bg;
    box-shadow: $default-box-shadow;
  }

  // dropUP menu
  .dropup > .dropdown-menu,
  &.navbar-fixed-bottom .dropdown > .dropdown-menu {
    margin-bottom: -2px;
    border-bottom: 2px solid $navbar-default-bg;
    box-shadow: $default-box-shadow-up;
  }

  // utility bar(s)
  .navbar-nav {
    margin: 0;

    // big heading
    > .heading {
      font-family: $font-family-brand;
      font-weight: $font-weight-light;
      letter-spacing: normal;
      font-size: 18px;
      height: $navbar-height;
      line-height: $navbar-height - 4px;
      > a {
        line-height: 22px;
        &:not([href]):hover {
          cursor: default;
        }
        .caret {
          top: -5px;
          left: 4px;
        }
      }
      div.dropdown-menu {
        padding-bottom: 0;
        > .scroll-wrapper {
          top: -4px;
        }
      }
      ul.dropdown-menu {
        padding-bottom: 10px;
        a {
          font-size: 18px;
          font-weight: $font-weight-light;
          padding-left: 45px;
          padding-right: 45px;
        }
      }
      .active a {
        background: inherit;
        color: $gray-light;
      }
    }

    // menu items/links
    > li {
      padding-left: $navbar-padding-horizontal;
      padding-right: $navbar-padding-horizontal;
      @include mobile-only {
        padding-left: 0;
        padding-right: 0;
      }
      &.divider {
        padding: 0;
        margin: 20px 3px;
        height: 20px;
        width: 1px;
        background: $navbar-default-divider;
      }
      > a {
        padding: 18px $navbar-padding-horizontal;
        &.horizontal-logo {
          padding-left: 16px;
        }
      }
      > a,
      > a:focus {
        color: $navbar-default-link-color;
      }
      > a:hover {
        color: $navbar-default-link-hover-color;
      }
    }
    > .disabled {
      > a,
      > a:hover {
        color: $navbar-default-link-disabled-color;
      }
    }
  }

  // dropdowns
  .dropdown-toggle {
    height: $navbar-height;
    .fa {
      margin-right: 5px;
    }
  }

  .horizontal-logo {
    .logo-image {
      background-image: url($logo-horizontal-navbar);
      // TODO find a permanent solution for this - the DD logo is fine in small format
      // @media (max-width: $screen-md) {
      //   width: 27px;
      //   background-image: url($logo-icon-navbar);
      // }
    }
  }

  // left nav
  .navbar-left {
    .dropdown-menu {
      text-align: left;
    }
    > .dropdown > .dropdown-menu {
      left: -22px;
      transform: translateX(0);
    }
  }

  // center nav
  .navbar-center {
    transition: margin-left $default-transition;
    margin-left: 0;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    .dropdown-menu {
      text-align: center;
    }
    > .dropdown > .dropdown-menu {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  // right nav
  .navbar-right {
    padding-right: 6px;
    .horizontal-logo {
      padding-left: $navbar-padding-horizontal;
      padding-right: $navbar-padding-horizontal;
    }
    .dropdown-menu {
      text-align: right;
    }
    > .dropdown > .dropdown-menu {
      right: -6px;
      transform: translateX(0);
    }
  }
}

// navbar buttons
.navbar-btn {
  padding: 0 $navbar-padding-horizontal;
  font-family: $font-family-sans-serif;
  font-size: $font-size-xsmall;
  line-height: 24px;
  .fa, .icon-badge .fa {
    font-size: $font-size-base;
    height: 24px;
    line-height: 1.5;
  }
  &.btn-lg {
    font-size: $font-size-large;
    margin: 5px 0;
    .fa {
      font-size: $font-size-large;
      line-height: 1.5;
    }
  }
  &:hover {
    color: $link-hover-color;
  }
  &.active,
  &.btn-toggle:active {
    @include navbar-btn-active;
  }
}

.navbar-text {
  cursor: default;
  line-height: 22px;
  padding: 0 $navbar-padding-horizontal;
  margin: 18px 0;
  position: relative;
  top: 1px;
  float: none;
  display: inline-block;
}

