.mobile-menu-layout {
  transition: right $default-transition, margin-right $default-transition;
  position: relative;
  right: 0;
  .mobile-menu {
    // background: $menu-bg-color;
    transition: right $default-transition, width $default-transition;
    position: fixed;
    top: 0;
    right: - $mobile-menu-width;
    width: $mobile-menu-width;
    height: 100vh;
    z-index: $zindex-menu;
  }
  &:after { @include menu-backdrop; }
}

@include mobile-only {
  // prevent page scrolling behind the menu
  .mobile-menu-open {
    overflow: hidden;
  }
  // display open menu
  .mobile-menu-open .mobile-menu-layout {
    right: $mobile-menu-width;
    .mobile-menu {
      right: 0;
    }
    .navbar-fixed-top {
      right: $mobile-menu-width;
      left: - $mobile-menu-width;
    }
    &:after { @include menu-backdrop-visible; }
  }
}

// smaller menu on really small screens to leave a space to tap outside to close it
@media (max-width: $mobile-menu-width + 60px) {
  .mobile-menu-layout .mobile-menu {
    width: $mobile-menu-width-xsmall;
    right: - $mobile-menu-width-xsmall;
  }
  .mobile-menu-open .mobile-menu-layout {
    right: $mobile-menu-width-xsmall;
    .navbar-fixed-top {
      right: $mobile-menu-width-xsmall;
      left: - $mobile-menu-width-xsmall;
    }
  }
}
