// General layout of this file:
// |-- THEME
// |
// |-- COLOUR
// |  |-- Brand colours
// |  |-- Action colours
// |  |-- Grays
// |  |-- State colours
// |  |-- Components
// |  |  |-- Body
// |  |  |-- Panel
// |  |  |-- Footer
// |  |  |-- Text
// |  |  |-- Links
// |  |  |-- Wells
// |  |  |-- Pop-overs, modals & tooltips
// |  |  |  |-- Pop-overs
// |  |  |  |-- Modals
// |  |  |  |-- Tooltips
// |  |  |-- Form elements
// |  |  |  |-- Inputs
// |  |  |  |-- Drop-downs
// |  |  |  |-- Buttons
// |  |  |  |  |-- Button states
// |  |  |  |-- Labels
// |  |  |-- Headings
// |  |  |-- Lists
// |  |  |-- Tables
// |  |  |-- Navbar
// |  |  |-- Nav
// |  |  |  |-- Tabs
// |  |  |-- Pagination
// |  |  |-- Jumbotron
// |  |  |-- Alerts
// |  |  |-- Progress bars
// |  |  |-- Panels
// |  |  |-- Breadcrumbs
// |  |  |-- Close icon
// |  |  |-- Horizontal rule
// |  |-- Miscelaneous other defaults/globals colours
// |     |-- Box shadows
// |
// |-- BACKGROUND IMAGES
// |  |-- Logos
// |  |-- Components
// |     |-- Jumbotron


@import './_paths-relative.scss';

// ========== Theme ========== //
$theme:                               'dark';
$brand-theme:                         'dd' !default;


// ========== Colour ========== //
// ---------- Brand colours ---------- //
$brand-primary:                     #5aad28 !default;
$brand-secondary:                   #455565 !default;
// ---------- Action colours ---------- //
$brand-info:                        #55abd9 !default;
$brand-success:                     #2e7d32 !default;
$brand-warning:                     #f5a81c !default;
$brand-danger:                      #e62535 !default;
// ---------- Grays ---------- //
$gray-base:                         #050505 !default;
$gray-darkest:                      #16191d !default;
$gray-darker:                       #22262b !default;
$gray-dark:                         #373b3f !default;
$gray:                              #595c60 !default;
$gray-light:                        #909294 !default;
$gray-lighter:                      #c7c8c9 !default;
$gray-lightest:                     #f3f3f4 !default;
// ---------- State colours ---------- //
// Default disabled
$disabled-color:                      $gray !default;
// ---------- Components ---------- //
// ---------- Body
$body-bg:                             $gray-darker !default;
// ---------- Panel
$panel-bg:                            $gray-dark !default;
// ---------- Footer
$footer-bg:                           $gray-dark !default;
// ---------- Text
$text-color:                          $gray-lightest !default;
$text-muted:                          $gray-light !default;
// ---------- Links
$link-active-color:                 white !default;
// ---------- Wells
$well-bg:                             $gray-dark !default;
$well-border:                         $gray-dark !default;
$well-color:                          $text-color !default;
// ---------- Pop-overs, modals & tooltips
// Pop-overs
$popup-bg-color:                      $gray-dark !default;
// Modals
$modal-content-bg:                    $panel-bg !default;
// Tooltips
$tooltip-bg:                          $brand-secondary !default;
// ---------- Form elements
// Inputs
$input-bg:                            $gray-darker !default;
$input-bg-focus:                      $gray-darkest !default;
$input-color:                         $gray-lighter !default;
$input-color-placeholder:             $gray-light !default;
$input-text-focus:                    $gray-lighter !default;
$input-border:                        $gray !default;
// Dropdowns
$dropdown-link-color:                 $gray-lightest !default;
// Buttons
// Default
$btn-default-color:                 white !default;
$btn-default-border:                white !default;
$btn-default-hover-color:           white !default;
$btn-default-hover-bg:              #383c40 !default;
$btn-default-active-bg:             #1e1f21 !default;
// Primary
$btn-primary-hover-color:           white !default;
$btn-primary-hover-bg:              #7fab65 !default;
$btn-primary-active-bg:             #3f6b25 !default;
// Secondary
$btn-secondary-hover-color:         white !default;
$btn-secondary-hover-bg:            #737f8b !default;
$btn-secondary-active-bg:           #333f4b !default;
// Toggle
$btn-toggle-color:                    $text-color !default;
$btn-toggle-hover-color:              $text-color !default;
$btn-toggle-hover-bg:                 $gray-dark !default;
// Info
$btn-info-hover-color:              white !default;
$btn-info-hover-bg:                 #2b8bb4 !default;
$btn-info-active-bg:                #056290 !default;
// Success
$btn-success-hover-color:           white !default;
$btn-success-hover-bg:              #619d64 !default;
$btn-success-active-bg:             #225d25 !default;
// Warning
$btn-warning-color:                 white !default;
$btn-warning-hover-bg:              #f7bd54 !default;
$btn-warning-active-bg:             #b77d14 !default;
// Danger
$btn-danger-hover-color:            white !default;
$btn-danger-hover-bg:               #ed6671 !default;
$btn-danger-active-bg:              #b81d2a !default;
// Disabled
$btn-disabled-color:                  $gray-darkest !default;
$btn-disabled-bg:                     $gray !default;
// Labels
$label-bg:                            $gray-dark !default;
// ---------- Headings
$headings-color:                      $text-color !default;
// ---------- Lists
$ol-bullet-bg:                        $gray !default;
$ol-bullet-color:                     $text-color !default;
// ---------- Tables
$table-bg-accent:                     rgba($gray-dark, 0.666) !default;
$table-bg-hover:                      rgba($gray-dark, 1) !default;
$table-border-color:                  $gray !default;
$table-outer-border-color:            $gray-light !default;
// ---------- Navbar
$navbar-default-border:               $gray-darkest !default;
$navbar-default-divider:              $gray-light !default;
// Navbar links
$navbar-default-link-color:           $gray-lightest !default;
$navbar-default-link-disabled-color:  $gray-light !default;
// ---------- Nav
// Tabs
$nav-tabs-active-link-hover-color:  white !default;
// ---------- Pagination
// Active
$pagination-active-color:             $gray !default;
// ---------- Jumbotron
$jumbotron-color:                     $text-color !default;
$jumbotron-bg:                        $gray-dark !default;
// ---------- Alerts
// State success
$state-success-text:                  lighten($brand-success, 10%) !default;
$state-success-border:                $input-border !default;
// State info
$state-info-text:                     lighten($brand-info, 10%) !default;
$state-info-border:                   $input-border !default;
// State warning
$state-warning-text:                  lighten($brand-warning, 10%) !default;
$state-warning-border:                $input-border !default;
// State danger
$state-danger-text:                   lighten($brand-danger, 10%) !default;
$state-danger-border:                 $input-border !default;
// ---------- Progress bars
$progress-bg:                         $gray-darkest !default;
// ---------- Panels
$panel-closed-bg:                     $panel-bg !default;
$panel-default-border:                $gray-light !default;
// ---------- Breadcrumbs
$breadcrumb-active-color:             $gray-light !default;
$breadcrumb-color:                    $gray-light !default;
// ---------- Close icon
$close-color:                         rgba($gray-light, 0.9) !default;
// ---------- Horizontal rule
$hr-border:                           $gray !default;
// ---------- Miscelaneous other defaults/globals colours ---------- //
// ---------- Box shadows
$default-box-shadow:                  1px 1px 0 0 rgba($gray-base, 0.75) !default;
$default-box-shadow-up:               2px 0 0 -1px rgba($gray-base, 0.75) !default;
$panel-box-shadow:                    -1px -1px 0 0 rgba($body-bg, 0.25), 0 1px 0 1px rgba($body-bg, 0.25), 1px 0 0 1px rgba($body-bg, 0.25), 1px 1px 0 1px rgba($body-bg, 0.25) !default;


// ========== Background images ========== //
// ---------- Logos ---------- //
$logo-icon-navbar:                   '#{$img-base-path}icon-white.svg' !default;
$logo-horizontal-navbar:             '#{$img-base-path}logo-white.svg' !default;
// ---------- Components ---------- //
// Jumbotron
$jumbotron-jumbo-bg-img:             '#{$img-base-path}jumbo-bg-dark.jpg' !default;
$jumbotron-jumbo-bg:                 $gray-darker url($jumbotron-jumbo-bg-img) no-repeat 50% 50% / cover !default;
