// mobile/desktop refers to the layout change from the vertical menu on the left to the right
// $desktop-breakpoint: 1200px
@mixin mobile-only {
  @media (max-width: $desktop-breakpoint-max) { @content; }
}

@mixin desktop-only {
  @media (min-width: $desktop-breakpoint) { @content; }
}

// small/large refers to a change in size of gutters and copy
// $grid-gutter-breakpoint: 768px
@mixin small-only {
  @media (max-width: $grid-gutter-breakpoint-max) { @content; }
}

@mixin large-only {
  @media (min-width: $grid-gutter-breakpoint) { @content; }
}

// selector for all hX heading tags and .hX heading classes
@mixin all-headings {
  h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 { @content; }
}

// selector for all col-* column tags
@mixin all-columns {
  [class^='col-'], [class*=' col-'] { @content; }
}

// light/dark theme selectors
@mixin light-only {
  @if ($theme == 'light') { @content; }
}

@mixin dark-only {
  @if ($theme == 'dark') { @content; }
}
