.btn-toolbar {
  margin: -5px;
  > * {
    float: left;
    margin: 5px;
  }
}

// button list with icons in it
.btn-list {
  padding-left: 0;
  margin-bottom: 0;
  li {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: -0.125em;
    }
  }
  a {
    display: block;
    font-size: $font-size-base;
    text-align: left;
    color: $link-color;
    .fa {
      color: white;
      background-color: $link-color;
      padding: 6px 6.5px;
      border-radius: 50%;
      margin-right: 8px;
      width: 28px;
      height: 28px;
    }
  }
  // &.btn-list-alternative {
  //   a {
  //     color: $text-color;
  //     .fa {
  //       color: $body-bg;
  //       background-color: $text-color;
  //     }
  //     &:hover {
  //       color: $link-color;
  //       text-decoration: none;
  //       .fa {
  //         background-color: $link-color;
  //       }
  //     }
  //   }
  // }
}

// a toolbar (row) of btn-links with separators between them
.btn-link-toolbar {
  margin: -14px -16px;
  display: block;
  &.pull-left { margin-right: 0; }
  &.pull-right { margin-left: 0; }
  .btn-separator {
    background: rgba($text-color, 0.2);
    width: 1px;
    height: 16px;
    display: inline-block;
    vertical-align: middle;
  }
  .btn {
    float: none;
  }
}

.btn-group > .btn + .dropdown-toggle {
  padding-left: $input-height-base;
  padding-right: 0;
}

