
.accordion-heading {
  cursor: pointer;
  &:hover * {
    color: $brand-info;
  }
}

.accordion {
  border-top: 1px solid $hr-border;
  border-bottom: 1px solid $hr-border;
  > .accordion-heading {
    position: relative;
    padding-top: 0.75 * $vertical-gutter;
    padding-bottom: 0.75 * $vertical-gutter;
    padding-right: 30px;
    &:before {
      @include fa-icon;
      content: $fa-var-angle-up;
      color: $brand-info;
      font-size: 3rem;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-1.6rem);
    }
    &.disabled {
      cursor: not-allowed;
    }
    &.collapsed:before {
      content: $fa-var-angle-down;
    }
  }
  &.accordion-sm > .accordion-heading {
    padding-top: $vertical-gutter / 2;
    padding-bottom: $vertical-gutter / 2;
  }
  > .accordion-content {
    // using :after instead of padding-bottom because that screws with the smoothness of the animation
    &:after {
      content: '';
      display: flex; // seems to work the best overall
      margin-top: $vertical-gutter;
    }
  }
}

.accordion-group {
  margin-bottom: $vertical-gutter;
  > .accordion:not(:last-child) {
    border-bottom: none;
  }
}
