@mixin menu-backdrop {
  content: '';
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($modal-backdrop-bg, $modal-backdrop-opacity);
  z-index: $zindex-menu - 1;
  transition: opacity $default-transition;
  visibility: hidden;
  opacity: 0;
}

@mixin menu-backdrop-visible {
  visibility: visible;
  opacity: 1;
}
