// size constants
$loader-size: 48px;
$placeholder-text-color: rgba($text-color, 0.15);
$placeholder-shimmer-color: $body-bg;

// spinner animation
@keyframes spinnerLoading {
  0% { transform: rotate(0turn); }
  100% { transform: rotate(1turn); }
}

// spinner css
.loader-spinner {
  overflow: hidden;
  position: relative;
  display: inline-block;
  width: $loader-size;
  height: $loader-size;
  vertical-align: middle;
  &.fade {
    opacity: 0;
    transform: scale(0.5, 0.5);
    transition: all 0.25s cubic-bezier(0.68, -0.55, 0.27, 1.55);
    &.in {
      opacity: 1;
      transform: scale(1, 1);
    }
    &.out {
      opacity: 0;
      transform: scale(0.5, 0.5);
      transition-delay: 0.75s;
      &:before, &:after {
        transition: all 0.5s ease-in;
      }
      &:before {
        border-color: $brand-primary rgba($brand-primary, 0.07);
      }
      &:after {
        border-color: $brand-primary;
      }
    }
  }
  &:before, &:after {
    content: '';
    display: block;
    position: absolute;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    margin-top: -50%;
    margin-left: -50%;
    transform: scale3d(0, 0, 0); //force gpu rendering
  }
  &:before {
    border-color: $brand-secondary rgba($text-color, 0.07);
    animation: spinnerLoading 2s ease-out infinite;
  }
  &:after {
    border-color: transparent $brand-primary;
    animation: spinnerLoading 2s ease-in-out infinite;
  }
  &.loader-sm {
    width: 0.5 * $loader-size;
    height: 0.5 * $loader-size;
    &:before, &:after {
      border-width: 0.5 * 4px;
    }
  }
  &.loader-lg {
    width: 2 * $loader-size;
    height: 2 * $loader-size;
    &:before, &:after {
      border-width: 2 * 4px;
    }
  }
}

// placeholder animation
@keyframes placeholderShimmer {
  to {
    background-position: 120% 0;
  }
}

// placeholder paragraph animation
@keyframes placeholderParagraphShimmer {
  to {
    background-position:
      120% 0, /* reflection */
      0 0, /* line 1 */
      0 1 * $line-height-base * $font-size-base, /* line 2 */
      0 2 * $line-height-base * $font-size-base, /* line 3 */
      0 3 * $line-height-base * $font-size-base; /* line 4 */
  }
}

.placeholder-title {
  background: $placeholder-text-color;
  height: $font-size-h3;
  margin-right: 70%;
  background-repeat: repeat-y;
  background-image: linear-gradient(90deg, rgba($placeholder-shimmer-color, 0), rgba($placeholder-shimmer-color, 0.3) 50%, rgba($placeholder-shimmer-color, 0.1));
  background-size: 20% 100%;
  background-position: -20% 0;
  animation: placeholderShimmer 1s infinite;
  margin-bottom: 16px;
  &:last-child {
    margin-bottom: 0;
  }
}

.placeholder-text {
  background: $placeholder-text-color;
  height: $font-size-base;
  background-repeat: repeat-y;
  background-image: linear-gradient(100deg, rgba($placeholder-shimmer-color, 0), rgba($placeholder-shimmer-color, 0.3) 50%, rgba($placeholder-shimmer-color, 0.1));
  background-size: 20% 100%;
  background-position: -20% 0;
  animation: placeholderShimmer 1s infinite;
}

.placeholder-image {
  background: $placeholder-text-color;
  height: 200px;
  background-repeat: repeat-y;
  background-image: linear-gradient(90deg, rgba($placeholder-shimmer-color, 0), rgba($placeholder-shimmer-color, 0.3) 50%, rgba($placeholder-shimmer-color, 0.1));
  background-size: 20% 100%;
  background-position: -20% 0;
  animation: placeholderShimmer 1s infinite;
  @extend %bottom-gutter;
  &.round {
    border-radius: 50%;
    width: 200px;
  }
}

.placeholder-paragraph {
  background-repeat: repeat-y;
  background-image:
    linear-gradient(100deg, rgba($placeholder-shimmer-color, 0), rgba($placeholder-shimmer-color, 0.5) 50%, rgba($placeholder-shimmer-color, 0) 80%), /* reflection */
    linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 1 */
    linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 2 */
    linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 3 */
    linear-gradient($placeholder-text-color $font-size-base, transparent 0); /* line 4 */
  background-size:
    20% 120px, /* reflection */
    24% 120px, /* line 1 */
    95% 120px, /* line 2 */
    91% 120px, /* line 3 */
    97% 120px; /* line 4 */
  background-position:
    -20% 0, /* reflection */
    0 0, /* line 1 */
    0 1 * $line-height-base * $font-size-base, /* line 2 */
    0 2 * $line-height-base * $font-size-base, /* line 3 */
    0 3 * $line-height-base * $font-size-base; /* line 4 */
  animation: placeholderParagraphShimmer 1s infinite;
  height: 100px;
  margin-bottom: 2rem;
  .panel & {
    background-image:
      linear-gradient(100deg, rgba($panel-bg, 0), rgba($panel-bg, 0.5) 50%, rgba($panel-bg, 0) 80%), /* reflection */
      linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 1 */
      linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 2 */
      linear-gradient($placeholder-text-color $font-size-base, transparent 0), /* line 3 */
      linear-gradient($placeholder-text-color $font-size-base, transparent 0); /* line 4 */
  }
}

