.jumbotron {
  // overflow: hidden;
  position: relative;
  margin-bottom: 0;
  @media (min-width: $screen-sm) {
    padding: 56px 0;
  }
  h1, h2 {
    @extend .main-heading;
  }
  p {
    font-style: italic;
    font-weight: $font-weight-normal;
    font-family: $font-family-serif;
    line-height: $line-height-small;
    margin-bottom: 1.5em;
    max-width: 700px;
    &:last-child { margin-bottom: -0.125em; }
  }
  &.jumbo {
    color: white;
    background: $jumbotron-jumbo-bg;
    padding: $vertical-gutter 0;
    @media (min-width: $desktop-menu-push-breakpoint) {
      background-size: auto;
    }
    @media (min-width: 1921px) {
      background-size: cover;
    }
    @media (min-width: $screen-sm) {
      padding: (2 * $vertical-gutter) 0;
    }
    @media (min-width: $screen-lg) {
      padding: (4 * $vertical-gutter) 0;
    }
    &.full {
      height: calc(100vh - #{$navbar-height});
      margin: 0;
      > div {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
