.drilldown-menu {
  background: $menu-bg-color;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;

  .sub-menu {
    background: $menu-alt-bg-color;
  }

  .desktop-menu & {
    border-top: 2px solid $menu-edge-color;
  }

  .horizontal-logo {
    color: white;
    .logo-image {
      background-image: url($logo-horizontal-white);
    }
    .logo-title {
      border-color: rgba(white, 0.8);
      color: white;
    }
  }

  > .scrollbars {
    height: 100%;
  }

  nav.drilldown {
    min-height: 100vh;

    // menu items: all levels
    ul {
      display: block;
      min-height: 100vh;
      // border-bottom: 2px solid $menu-edge-color;
      // padding: 0 0 16px 0;
      &:after {
        content: '';
        display: block;
        width: 100%;
        margin-top: 16px;
        border-bottom: 2px solid $menu-edge-color;
      }

      li {
        &:before,
        &:after {
          content: '';
          border-bottom: 1px solid transparent;
          display: block;
          margin: 0 20px;
        }
        &.heading {
          &:before,
          &:after {
            display: none;
          }
        }
        a {
          color: $menu-link-color;
          line-height: 1.15;
          padding: 8px 20px 10px 32px;
          border-left: 3px solid transparent;
          margin-left: 1px;
          .fa:first-child {
            margin-right: 10px;
          }
          // accordion
          &.next {
            position: relative;
            padding-right: 38px;
            // caret
            &:before {
              color: $menu-link-color;
              @include fa-icon;
              content: $fa-var-angle-right;
              position: absolute;
              top: 50%;
              right: 26px;
              transform: translateY(-50%);
            }
          }
          // &:hover {
          //   background: $brand-info;
          //   color: $menu-base-color;
          //   z-index: 1; // z-index to cover the li:before and li:after lines
          //   &:before, &:after {
          //     border-color: transparent;
          //   }
          // }
        }
        &.active > a {
          color: $menu-base-color;
          border-left-color: $menu-link-color;
          background: rgba($menu-base-color, 0.15);
          &.next:before {
            color: $menu-base-color;
          }
        }
        &.status-success > a {
          border-left-color: $brand-primary;
        }
        &.status-warning > a {
          border-left-color: $brand-warning;
        }
        &.status-danger > a {
          border-left-color: $brand-danger;
        }
        &.status-default > a {
          border-left-color: $menu-link-color;
        }
      }
    }

    // menu items: 1st (top) level
    > ul {
      padding: 0;

      &:after {
        margin-top: 0;
      }

      > li {
        &:before,
        &:after {
          content: '';
          border-bottom: 1px solid $menu-line-color;
          display: block;
          margin: 0 20px;
          pointer-events: none;
        }
        &:first-child:before,
        // &:first-child:after,
        &:last-child:after {
          display: none;
        }
        > a {
          padding: 17px 16px 19px 16px;
          border-left-width: 4px;
          margin-left: 0;
        }
        // hide the li:before and li:after lines
        &.active > a {
          box-shadow: 0 -2px 0 0 $menu-bg-color, 0 2px 0 0 $menu-bg-color;
          background: $menu-line-color;
          &.next:before {
            color: $menu-base-color;
          }
        }
        &.menu-header {
          border-bottom: 2px solid $menu-edge-color;
          height: $navbar-height;
          &:after {
            display: none;
          }
          + li {
            &:before {
              display: none;
            }
            &.active a {
              box-shadow: 0 2px 0 0 $menu-bg-color;
            }
          }
        }
        &.active:last-child > a {
          box-shadow: 0 -2px 0 0 $menu-bg-color;
        }

        // submenu: 2nd level (and after)
        .heading {
          a.back {
            font-family: $font-family-serif;
            font-style: italic;
            font-size: $font-size-small;
            position: relative;
            padding: 21px 20px 0 20px;
            height: $navbar-height;
            border-left: none;
            border-bottom: 2px solid $menu-edge-color;
            &:before {
              @include fa-icon;
              content: $fa-var-angle-left;
              margin-right: 10px;
            }
            &:hover {
              color: $menu-base-color;
            }
          }
          .title {
            @extend .subscript-heading;
            // font-size: $font-size-small;
            color: mix($menu-base-color, $menu-bg-color, 90%);
            margin: 20px 20px 20px 20px;
            line-height: 17px;
          }
        }
      }
    }
  }
}
