.site-header {
  // fill the space for .navbar-fixed-top
  padding-bottom: $navbar-height;
  .navbar-fixed-top {
    // conceal the navbar when scrolling down on mobile
    @include mobile-only {
      &.navbar-hide {
        transform: translateY( - $navbar-height);
        .dropdown-menu {
          display: none;
        }
      }
    }
  }
}

// mobile menu toggle button
.mobile-menu-toggle {
  .fa-bars {
    font-size: 21px;
    line-height: 1.1;
  }
}
