@media (min-width: $screen-xl) {
  @include make-grid(xl);
}

@media (min-width: $screen-xxl) {
  @include make-grid(xxl);
}

// small grid (mobile)
.row {
  padding-left: 20px - $grid-gutter-width-small;
  padding-right: 20px - $grid-gutter-width-small;

  // large grid (desktop)
  @include large-only {
    padding-left: 40px - $grid-gutter-width;
    padding-right: 40px - $grid-gutter-width;
    @include all-columns {
      padding-left: $grid-gutter-width;
      padding-right: $grid-gutter-width;
    }
  }

  &.no-gutter {
    padding-left: 0;
    padding-right: 0;
    margin-left: - $grid-gutter-width-small;
    margin-right: - $grid-gutter-width-small;
    @include large-only {
      margin-left: - $grid-gutter-width;
      margin-right: - $grid-gutter-width;
    }
  }

  &.row-sm {
    max-width: $screen-sm;
    @media (min-width: $screen-sm + $grid-gutter-width) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.row-md {
    max-width: $screen-md;
    @media (min-width: $screen-md + $grid-gutter-width) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.row-lg {
    max-width: $screen-lg;
    @media (min-width: $screen-lg + $grid-gutter-width) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.row-xl {
    max-width: 1400px;
    @media (min-width: $screen-xl + $grid-gutter-width) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.row-xxl {
    max-width: 1600px;
    @media (min-width: $screen-xl-max + $grid-gutter-width) {
      margin-left: auto;
      margin-right: auto;
    }
  }
}

// x-small grid inside modals/panels
.modal-content,
.panel-body {
  // .tab-pane {
  .row {
    padding-left: 0;
    padding-right: 0;
    margin-left: -$grid-gutter-width-xsmall;
    margin-right: -$grid-gutter-width-xsmall;
    @include all-columns {
      padding-left: $grid-gutter-width-xsmall;
      padding-right: $grid-gutter-width-xsmall;
    }
  }
}

.col-center {
  float: none;
  margin-left: auto;
  margin-right: auto;
}

.container-lg,
.container-xl,
.container-xxl {
  @include container-fixed;
  width: 100%;
}

.container-lg {
  max-width: $screen-xxl - $grid-gutter-width;
}

.container-xl {
  max-width: 1600px - $grid-gutter-width;
}

.container-xxl {
  max-width: 1920px;
}
