// When a label spans onto multiple lines it messes up the positioning of control-feedback icons. One solution is to wrap everything besides the label in a position: relative div which I've called .control-wrapper
.control-wrapper {
  position: relative;
}

label {
  display: block;
  color: $form-label-color;
  font-weight: $font-weight-normal;
  line-height: $headings-line-height;
  margin-bottom: $form-label-margin;
  &.disabled,
  .disabled & {
    color: $disabled-color;
    cursor: not-allowed;
    font-style: italic;
  }
  .info {
    font-style: italic;
    font-weight: $font-weight-light;
  }
}

.form-control {
  box-shadow: none;
  transition: padding-left 0.05s ease-in-out; // animation for showing/hiding icons
  border-color: $input-border;

  &.btn {
    background-color: $input-bg;
  }

  &#{&} {
    font-size: $input-font-size-base;
    font-weight: $font-weight-normal;
    // override native browser styles (eg iOS safari)
    -webkit-appearance: none; /* stylelint-disable-line */
    -moz-appearance: none; /* stylelint-disable-line */
    appearance: none; /* stylelint-disable-line */
  }

  // disable IE's show/hide password button and IE's clear button "x"
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }

  &[disabled], fieldset[disabled] &, &[readonly]:not(.flatpickr-input) {
    color: $disabled-color;
    font-style: italic;
    @include placeholder-selector {
      color: $disabled-color;
    }
  }

  @include placeholder-selector {
    font-style: italic;
    font-weight: $font-weight-normal;
  }

  &.input-sm,
  .form-group-sm &,
  .input-group-sm & {
    font-size: $font-size-small;
    height: $input-height-small;
  }

  &.input-lg,
  .form-group-lg &,
  .input-group-lg & {
    font-size: $font-size-large;
    height: $input-height-large;
  }
}

textarea.form-control {
  resize: vertical;
  line-height: 1.33333;
  min-height: $input-height-large;
  &.input-sm,
  .input-group-sm &,
  .form-group-sm & {
    padding-top: $padding-small-vertical + 2px;
    padding-bottom: $padding-small-vertical + 2px;
  }
  &.input-lg,
  .input-group-lg &,
  .form-group-lg & {
    padding-top: $padding-large-vertical - 3px;
    padding-bottom: $padding-large-vertical - 3px;
  }
}

select.form-control,
.form-group .dropdown-toggle.form-control,
.form-group-lg select.form-control,
.form-group-sm select.form-control {
  line-height: $line-height-base;
}

select.form-control {

  // caret
  &:not([multiple]) {
    // remove the native browsers carets
    -webkit-appearance: none; /* stylelint-disable-line */
    -moz-appearance: none; /* stylelint-disable-line */
    appearance: none; /* stylelint-disable-line */
    &::-ms-expand { display: none; }

    // add padding-right for caret
    padding-right: $input-height-base;
    background-position: right ($input-height-base / 2 - 4px) center;
    &.input-sm,
    .input-group-sm &,
    .form-group-sm & {
      padding-right: $input-height-small - 1;
      background-position: right ($input-height-small / 2 - 4px) center;
    }
    &.input-lg,
    .input-group-lg &,
    .form-group-lg & {
      padding-right: $input-height-large;
      background-position: right ($input-height-large / 2 - 4px) center;
    }

    // add custom blue caret
    @include background-caret($link-color);
  }

  // multiselect
  &[multiple] {
    // ideal padding for mobile browsers (Android/iOS)
    padding-top: $padding-base-vertical + 4px;
    padding-bottom: $padding-base-vertical + 4px;
    &.input-sm,
    .input-group-sm &,
    .form-group-sm & {
      padding-top: $padding-small-vertical + 4px;
      padding-bottom: $padding-small-vertical + 4px;
    }
    &.input-lg,
    .input-group-lg &,
    .form-group-lg & {
      padding-top: $padding-large-vertical;
      padding-bottom: $padding-large-vertical;
    }

    // on desktop it looks better without padding
    .is-desktop & {
      padding: 0;
    }

    // focus selected option colors (styling for native selects is extremely limiting)
    &:focus {
      option:checked {
        color: white;
        background: $brand-info linear-gradient(0deg, $brand-info 0%, $brand-info 100%);
      }
    }
  }

  // options
  option {
    color: $input-color;
    padding: ($padding-base-vertical - 1px) $padding-base-horizontal;
  }
  &.input-sm,
  .input-group-sm &,
  .form-group-sm & {
    option {
      padding: ($padding-small-vertical) $padding-small-horizontal;
    }
  }
  &.input-lg,
  .input-group-lg &,
  .form-group-lg & {
    option {
      padding: ($padding-large-vertical - 4px) $padding-large-horizontal;
    }
  }
}

input[type='file'].form-control {
  height: auto;
}

.form-group {
  position: relative; // for .form-control-feedback and other icon positioning
  margin-bottom: 0.75 * $vertical-gutter;
}

// .has-icon is for an icon on the left of the field
// .has-feedback is for success/error validation icon
.has-icon,
.has-feedback {
  position: relative;
  .form-control {
    padding-left: $input-height-base;
    padding-right: $padding-base-horizontal;
  }
  .input-sm.form-control,
  .input-group-sm .form-control,
  &.input-group-sm .form-control,
  &.form-group-sm .form-control {
    padding-left: $input-height-small;
    padding-right: $padding-small-horizontal;
    padding-bottom: $padding-small-vertical + 1px;
  }
  .input-lg.form-control,
  .input-group-lg .form-control,
  &.input-group-lg .form-control,
  &.form-group-lg .form-control {
    padding-left: $input-height-large;
    padding-right: $padding-large-horizontal;
    padding-bottom: $padding-large-vertical + 2px;
  }
  // feedback icon (medium, small, large)
  label ~ .form-control-feedback {
    top: $font-size-base + $form-label-margin + 1px; // Height of the `label` + its margin + a border(?)
  }
}

.form-control-feedback {
  display: block;
  font-family: FontAwesome;
  top: 0;
  left: 2px;
  z-index: 10;
  color: $link-color;
  &:before {
    .has-success & { content: $fa-var-check; }
    .has-warning & { content: $fa-var-exclamation-circle; }
    .has-error & { content: $fa-var-exclamation; }
    .has-info & { content: $fa-var-info-circle; }
  }
  .input-lg ~ &,
  .input-group-lg &,
  .form-group-lg & {
    font-size: $font-size-large;
    width: $input-height-large;
    height: $input-height-large;
    line-height: $input-height-large;
  }
  .input-sm ~ &,
  .input-group-sm &,
  .form-group-sm & {
    font-size: $font-size-small;
    width: $input-height-small;
    height: $input-height-small;
    line-height: $input-height-small;
  }
}

// this seems hacky, but it also seems to work (to position a left-icon AFTER an .input-group-addon)
.input-group-addon + .form-control-feedback {
  position: relative;
  height: 0;
}

p, span, div {
  &.help-block {
    line-height: $headings-line-height;
    margin: 6px 0 20px 0;
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    &, .has-success & {
      color: $disabled-color;
    }
  }
}

.radio-inline + .radio-inline,
.checkbox-inline + .checkbox-inline {
  margin-left: 24px; // space out consecutive inline controls
}

// Feedback states
.has-success {
  @include form-control-validation($state-success-text, $state-success-border, $state-success-bg);
}

.has-warning {
  @include form-control-validation($state-warning-text, $state-warning-border, $state-warning-bg);
}

.has-error {
  @include form-control-validation($state-danger-text, $state-danger-border, $state-danger-bg);
}
