.checkbox {
  padding-left: 20px;
  cursor: default;
  margin-top: 0;
  margin-bottom: 0;
  & + .checkbox:not(.checkbox-inline) {
    margin-top: 5px;
  }
  label {
    font-size: $font-size-small;
    cursor: default;
    display: inline-block;
    position: relative;
    padding-left: 5px;
    margin-top: 0;
    line-height: 1;
    .table & {
      min-height: 17px; // just to make it fit nicely inline with table row
    }
    &:last-child {
      margin-bottom: -6px;
    }
    &:before {
      content: '';
      display: inline-block;
      position: absolute;
      width: 17px;
      height: 17px;
      left: 0;
      margin-left: -20px;
      border: 1px solid $input-border;
      border-radius: $input-border-radius;
      background-color: $input-bg;
      margin-top: -2px;
    }
    &:after {
      display: inline-block;
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 0;
      margin-left: -20px;
      padding-left: 3px;
      padding-top: 1px;
      font-size: 11px;
      color: $brand-info;
      font-family: FontAwesome;
    }
  }
  input[type='checkbox'] {
    opacity: 0;
    // focus state is commented out for consistency because it would only work when input is before the label and wont work if its inside the label
    // &:focus + label:before {
    //   outline: thin dotted;
    //   outline: 5px auto -webkit-focus-ring-color; /* stylelint-disable-line */
    //   outline-offset: -2px;
    // }
  }
  // checked state
  label.checked,
  input[type='checkbox']:checked + label {
    &:after {
      content: '\f00c';
    }
  }
  // disabled state
  label.disabled,
  input[type='checkbox']:disabled + label {
    cursor: not-allowed;
    pointer-events: none;
    color: $disabled-color;
    font-style: italic;
    &:after {
      color: $disabled-color;
      font-style: normal;
    }
  }
}

// form-inline checkbox
@media (min-width: $screen-sm) {
  .form-inline {
    > .form-group:first-child {
      .checkbox:first-child {
        padding-left: 5px;
      }
    }
  }
}
