// jQuery Scrollbar
@import './node_modules/jquery.scrollbar/jquery.scrollbar';

$scrollbar-spacer: 4px !default;
$scrollbar-thickness: 4px !default;

.scrollbars-light {
  &.scrollbar-inner > .scroll-element {
    .scroll-element_track {
      background-color: rgba(white, 0.5);
    }
    .scroll-bar,
    &:hover .scroll-bar,
    &.scroll-draggable .scroll-bar {
      background-color: white;
    }
  }
}

.scrollbars-dark {
  &.scrollbar-inner > .scroll-element {
    .scroll-element_track {
      background-color: rgba($gray-base, 0.2);
    }
    .scroll-bar,
    &:hover .scroll-bar,
    &.scroll-draggable .scroll-bar {
      background-color: rgba($gray-base, 0.3333);
    }
  }
}

.scrollbars-inverse {
  @extend .scrollbars-#{$theme};
}

.scrollbar-inner {
  @include light-only {
    @extend .scrollbars-dark;
  }
  @include dark-only {
    @extend .scrollbars-light;
  }
  > .scroll-element {
    @media screen and (min-width: $screen-md) {
      opacity: 0.333333;
    }
    .scroll-element_outer {
      border-radius: 0;
    }
    .scroll-element_track {
      border-radius: 100px;
    }
    &.scroll-x {
      bottom: 0;
      left: $scrollbar-spacer;
      right: $scrollbar-spacer;
      width: auto;
      height: 16px;
      .scroll-element_track {
        height: $scrollbar-thickness;
        bottom: $scrollbar-thickness;
        top: auto;
      }
      .scroll-bar {
        height: $scrollbar-thickness;
        bottom: $scrollbar-thickness;
        top: auto;
        &:after {
          content: '';
          display: block;
          position: absolute;
          height: 25px;
          width: 100%;
          top: -15px;
        }
      }
    }
    &.scroll-y {
      right: 0;
      top: $scrollbar-spacer;
      bottom: $scrollbar-spacer;
      height: auto;
      width: 16px;
      .scroll-element_track {
        width: $scrollbar-thickness;
        right: $scrollbar-thickness;
        left: auto;
      }
      .scroll-bar {
        width: $scrollbar-thickness;
        right: $scrollbar-thickness;
        left: auto;
        &:after {
          content: '';
          display: block;
          position: absolute;
          width: 25px;
          height: 100%;
          left: -15px;
        }
      }
    }
  }
  &:hover > .scroll-element,
  > .scroll-element.scroll-draggable {
    opacity: 1;
  }
}
