// default dropdown MENU styling (not full-width)
.dropdown-menu {
  z-index: $zindex-dropdown;
  border-radius: 0;
  font-size: $input-font-size-base;
  box-shadow: $default-box-shadow;
  background-clip: inherit; // fixes a bug with 1px gap on the right when scrollbar is used

  .dropdown &,
  .dropup & {
    margin: 0;
  }
  .divider {
    margin: 6px ($padding-base-vertical * 1.5);
  }
  > li > a {
    padding: 8px 20px;
    white-space: normal;
    word-break: normal;
    &:focus {
      color: inherit;
    }
  }

  // for scrollbars inside of dropdowns use this structure: div.dropdown-menu > div.scrollbars > ul.dropdown-menu
  .scrollbars {
    max-height: 300px;
    // now that there's a .dropdown-menu inside a .dropdown-menu we need to remove all the edges' styling from the inner one
    .dropdown-menu {
      display: block;
      position: static;
      float: none;
      margin: 0;
      padding: 0;
      border: 0;
      box-shadow: none;
    }
  }
}

// SMALL dropdown-menu
.input-sm ~ .dropdown-menu,
.input-group-sm .dropdown-menu,
.form-group-sm .dropdown-menu {
  font-size: $font-size-small;
}

// LARGE dropdown-menu
.input-lg ~ .dropdown-menu,
.input-group-lg .dropdown-menu,
.form-group-lg .dropdown-menu {
  font-size: $font-size-large;
}

// IMITATION SELECT dropdown-menu (full-width)
.form-control ~ .dropdown-menu,
.input-group > .dropdown > .dropdown-menu,
.input-group > .dropup > .dropdown-menu {
  width: 100%;
  background: $input-bg-focus;
  li {
    a {
      cursor: default;
      &:hover {
        background: $link-color;
        color: white;
      }
    }
  }
}

.dropdown-toggle {
  .caret {
    position: absolute;
    top: 45%;
    right: 18px;
  }

  // standard dropdown BUTTON
  &.btn,
  &.form-control {
    position: relative;
    padding-right: $input-height-base;
  }

  // IMITATION SELECT button
  &.form-control {
    text-align: left;
    cursor: default;
    &:focus,
    &:active {
      outline: none; // disable .btn:focus outline
    }
    &:not(:focus):hover {
      color: $input-color;
    }
    .caret {
      border-bottom: 0;
      border-top: $caret-width-base solid $link-color;
    }
    // special dropdown-menu/drop-up as per designs
    .open & {
      border-color: $input-border-focus;
      background-color: $input-bg-focus;
      .caret { display: none; }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: 2px;
        background: $input-border;
        border: 1px solid $input-bg-focus;
        border-left-width: ($padding-base-vertical * 1.5);
        border-right-width: ($padding-base-vertical * 1.5);
      }
      ~ .dropdown-menu {
        border-color: $input-border-focus;
        box-shadow: none;
      }
    }
    //drop down/up specific styles
    .open.dropdown & {
      &.form-control {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
      &:after {
        bottom: -2px;
        border-top: none;
      }
      ~ .dropdown-menu {
        border-top: none;
        border-bottom-left-radius: $input-border-radius;
        border-bottom-right-radius: $input-border-radius;
      }
    }
    .open.dropup & {
      &.form-control {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      &:after {
        top: -2px;
        border-bottom: none;
      }
      ~ .dropdown-menu {
        border-bottom: none;
        border-top-left-radius: $input-border-radius;
        border-top-right-radius: $input-border-radius;
      }
    }
  }
}

// caret position and padding
.dropdown,
.dropup {
  .caret {
    position: absolute;
    top: 45%;
    right: ($input-height-base / 2 - 4px);
  }
}

.btn-sm,
.input-sm,
.input-group-sm,
.form-group-sm {
  .dropdown-toggle {
    padding-right: $input-height-small;
    .caret {
      right: ($input-height-small / 2 - 4px);
    }
  }
}

.btn-lg,
.input-lg,
.input-group-lg,
.form-group-lg {
  .dropdown-toggle {
    padding-right: $input-height-large;
    .caret {
      right: ($input-height-large / 2 - 4px);
    }
  }
}

// dropdown buttons inside input groups should expand
.input-group {
  .dropdown,
  .dropup {
    display: table-cell;
  }
  .dropdown-toggle {
    width: 100%;
    text-align: left;
  }
}
