// a logo image with a text title on the right
.horizontal-logo {
  overflow: hidden;
  display: block;
  padding: 18px 20px 16px 20px;
  line-height: 24px;
  height: 60px;
  img {
    max-height: 24px;
    margin: -1px 0 0 0;
  }
  .logo-image {
    float: left;
    background: url($logo-horizontal) no-repeat 50% 50% / contain;
    height: $logo-horizontal-height;
    width: $logo-horizontal-width;
    margin: -1px 0 0 0;
    &[src] {
      // allows img.logo-image to be used
      background-image: none;
      width: auto;
    }
  }
  .logo-title {
    display: table;
    float: left;
    color: $text-color;
    font-family: $font-family-brand;
    font-weight: $font-weight-normal;
    font-size: $font-size-xsmall;
    line-height: $font-size-xsmall;
    letter-spacing: normal;
    padding-left: 8px;
    border-left: 1px solid rgba($text-color, 0.8);
    height: 30px;
    margin: -4px 0 -4px 9px;
    text-transform: lowercase; // to enforce consistency but devs can override if needed
    > span {
      display: table-cell;
      vertical-align: middle;
    }
  }
}

// a logo image with a text title on the right
.horizontal-logo-solid {
  padding: 0;
  height: $navbar-height;
  .logo-image {
    margin: 0;
    background: $brand-primary url($logo-horizontal-white) no-repeat 16px 48% / contain;
    height: 100%;
    width: 138px;
    background-size: 109px;
  }
  .logo-title {
    color: white;
    background: $brand-secondary;
    margin: 0;
    border: none;
    height: 100%;
    padding: 12px;
  }
}
