.badge {
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-normal;
  font-size: $font-size-xsmall;
  text-align: center;
  line-height: 16px;
  height: 16px;
  min-width: 16px;
  position: relative;
  top: -1px; // make inline with normal paragraph text
  &, .btn & {
    padding: 0 4px;
  }
  .list-group-item > & {
    margin: 5px 0;
  }
}

.badge-dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  color: transparent;
  line-height: 0.5;
  background: $badge-bg;
}

// this new icon-badge component (usually) sits inside a button and defines positioning of icons with badges
.icon-badge {
  position: relative;
  min-width: 1em;
  display: inline-block;
  margin-top: -0.5em;
  vertical-align: middle;
  .fa {
    line-height: 1.6;
    position: absolute;
  }
  .badge {
    z-index: 1;
    position: relative;
    top: -8px;
    left: 0.6em;
    &.badge {
      margin: 0;
    }
    &:empty {
      visibility: hidden;
      display: inline-block;
    }
  }
  .badge-dot {
    z-index: 1;
    position: relative;
    top: -7px;
    left: 0.5em;
    margin: 0 4px 0 0;
  }
}
