.panel-heading {
  border-bottom: none;
  position: relative;
  &.disabled {
    cursor: not-allowed;
  }
  // collapsible caret (as per accordion.scss)
  &[data-toggle='collapse'] {
    cursor: pointer;
    &:hover {
      .panel-title {
        color: $brand-info;
      }
    }
    &:before {
      @include fa-icon;
      content: $fa-var-angle-up;
      color: $brand-info;
      font-size: 3rem;
      position: absolute;
      top: 50%;
      right: $panel-heading-padding;
      transform: translateY(-1.5rem);
    }
    &.collapsed:before {
      content: $fa-var-angle-down;
    }
    > .panel-title,
    > :first-child {
      padding-right: 24px;
    }
  }
  + .panel-body,
  + .panel-collapse .panel-body {
    padding-top: 0;
  }
  .panel-title {
    font-weight: $font-weight-light;
    font-size: $font-size-h4;
    line-height: 1.1;
    margin-top: -0.125em;
    margin-bottom: -0.125em;
  }
  .panel-subtitle {
    font-weight: $font-weight-light;
    font-size: $font-size-small;
    margin-top: 0.4em;
    line-height: 1;
  }
}

// .panel-body {}

.panel-footer {
  border-top: none;
  overflow: hidden;
  padding-top: 2px;
  .btn-link-toolbar {
    margin-top: 0;
  }
}

.panel {
  border: none;
  box-shadow: $panel-box-shadow;
  margin: 1px 2px $line-height-computed 1px;


  .panel-group > &,
  .panel & {
    margin-bottom: 2px;
  }

  > .panel-body + .table,
  > .panel-body + .table-responsive,
  > .table + .panel-body,
  > .table-responsive + .panel-body {
    border-top: none;
    margin-bottom: 0;
  }

  @media (max-width: $screen-md-max) {
    .btn-link-toolbar {
      margin-left: -5px;
      margin-right: -5px;
      .btn {
        padding-left: 5px;
        padding-right: 5px;
        font-size: $font-size-small;
        .fa {
          font-size: $font-size-small;
        }
      }
    }
  }
}

.panel-group {
  .panel-heading {
    // background-color inactive to active transition
    &, & ~ * {
      transition: all $default-transition;
    }
    &.active,
    &.active ~ * {
      transition: all $default-transition, background 0.1s ease-out;
      background: $panel-bg;
    }
    &.collapsed,
    &.collapsed ~ * {
      // background: rgba($gray-light, 0.25);
      background: $panel-closed-bg;
    }

    + .panel-collapse > .panel-body,
    + .panel-collapse > .list-group {
      border-top: none;
    }
    dl {
      margin-top: 2px;
    }
  }

  .panel-footer {
    border-top: none;
    overflow: hidden;
    + .panel-collapse .panel-body {
      border-bottom: none;
    }
  }
}

// new panel variants
.panel-secondary {
  @include panel-variant($panel-secondary-border);
}

.panel-indented {
  .panel-heading {
    padding-left: 80px;
    .panel-icon {
      position: absolute;
      margin: -20px 0 0 0;
      top: 50%;
      left: 20px;
      padding: 0;
      width: 100%; // fixes some svgs in IE11
      max-width: 40px;
      max-height: 40px;
    }
  }
  .panel-body {
    padding-left: 80px;
  }
  .panel-footer {
    padding-left: 80px;
  }
}
