// This is just the barebones styles for the drilldown menu functionality

.drilldown {
  padding: 0;
  overflow: hidden;
  transform: translateZ(0);

  ul {
    width: 100%;
  }

  > ul {
    left: 0;
    position: relative;
    transition: left 0.3s ease, height 0.3s ease; // looks smoother than the $default-transition
    overflow-y: visible;
  }

  li {
    position: static;
    ul {
      display: none;
      left: 100%;
      position: absolute;
      top: 0;
    }
  }

  ul, li {
    list-style: none;
    margin: 0;
  }

  a {
    display: block;
  }
}
