.progress-steps {
  counter-reset: step;
  // accordion progress steps
  &.accordion-group {
    .accordion {
      .accordion-heading {
        padding-top: 16px;
        padding-bottom: 14px;
        &:before {
          display: none;
        }
      }
    }
  }
  // tabbed progress steps
  &.nav-tabs {
    border-bottom-color: transparent;
    padding: 0;
    > li {
      padding: 0;
      &.active > a,
      &.active > a:hover,
      &.active > a:focus {
        border-bottom-color: transparent;
      }

      &:not(:last-child) {
        position: relative;
        &:after {
          vertical-align: middle;
          font-family: FontAwesome;
          content: $fa-var-angle-right;
          color: $hr-border;
          position: absolute;
          right: -4px;
        }
      }
    }
  }
}

.progress-step {
  counter-increment: step;
  color: $brand-info;
  &:before {
    content: counter(step);
    color: $body-bg;
    background: $brand-info;
    vertical-align: middle;
    text-align: center;
    transform: scale(0.6);
    font: normal 20px $font-family-base;
    line-height: 22px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    top: -2px;
    right: 4px;
  }
  .complete & {
    color: $brand-info;
    &:before {
      content: $fa-var-check;
      font-family: FontAwesome;
      font-size: 14px;
      line-height: 24px;
      background: $brand-info;
    }
  }
  .disabled & {
    font-style: italic;
    color: $btn-link-disabled-color;
    &:before {
      background: $btn-link-disabled-color;
    }
  }
  .active & {
    color: $nav-tabs-active-link-hover-color;
    &:before {
      background: $ol-bullet-bg;
      color: $ol-bullet-color;
    }
  }
}
